//page-my
.page-my {
  min-height:calc(100vh - 70px - 84px);
  background: rgb(247, 247, 247);

  .my-flex {
    margin:0 -14px;
    flex-wrap:nowrap;
    align-items:stretch;

    .col-summary,
    & > .col-right {
      padding:0 14px;
      width:38%;
    }

    & > .col-right {
      width:62%;
    }


    &._full {
      .col-summary {
        width:100%;
      }
    }

    &:not(._full) {
      padding:30px 0;

      .summary-component {
        overflow:hidden;
        border-radius: 16px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
      }
    }

    .right-outer {
      height:100%;
      text-align: left;
      background:#fff;
      overflow:hidden;
      border-radius: 16px;
      padding:38px 32px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

      .sec,
      .sec2 {
        background:#fff;
      }

      .sec-top {
        padding-top:24px;
      }

      .full-height {
        height: calc(100% - 44px);
        padding:0;
        background:rgba(247, 247, 247, 1);

        .col-top {
          background:#fff;

          .top-first {
            padding:24px 0;
          }
        }
      }

      .m-main {
        padding:0;
      }

      .right-header {
        padding-bottom:20px;
        border-bottom:1px solid rgba(233, 233, 233, 1);
        text-align: left;
        background:#fff;

        .tit {
          display:inline-block;
          line-height:1.1;
          font-size:20px;
          font-weight:700;
        }
      }

      .bg-box {
        background:rgba(247, 247, 247, 1);
        border-radius: 6px;
        padding:20px;
      }

      & > .bg-box {
        height:100%;
        padding:0;
      }

      &.t2 {
        padding-bottom:0;

        .right-header {
          border-bottom:0;
        }

        .sec-wrap,
        .full-height {
          background:rgba(247, 247, 247, 1);
          margin:0 -32px;
          padding-bottom:38px;
          width:auto;
        }

        .full-height {
          padding-top:24px;

          & > .m-main {
            padding:0 32px;
          }

          .col-top {
            background:rgba(247, 247, 247, 1);
          }
        }
      }

      &.t3 {
        .bg-box {
          background:#fff;
        }

        .full-height,
        section {
          padding-top:24px;
          background:#fff;

          .col-top {
            background:#fff;

            .noti {
              br {
                display:none;
              }
            }
          }

          .btt-btn {
            .chk-label {
              .txt,
              .txt > * {
                white-space: nowrap;
              }
            }
          }

          .bbs-container {
            padding-top:0;
          }
        }

        .sec-tab {
          padding:0;
          border:0;

          .tab-nav {
            padding:0;
          }
        }
      }

      .card-flex > li {
        width: 50%;
      }
    }
  }

  .sec-info {
    background:#fff;
    margin:0;
    padding-top:12px;
  }

  .my-info {
    .name {
      font-weight: 700;
      font-size:18px;
      display:block;
    }

    .dec {
      color:#757575;
    }

    .ball-box {
      background:#F0F5F8;
      border:1px solid #E9E9E9;
      border-radius:6px;
      margin-top:18px;
      padding:20px 19px 12px;

      .ball-flex {
        justify-content: space-between;
        padding-bottom:10px;
        border-bottom:1px solid #E9E9E9;
        margin-bottom:9px;
      }

      .ball {
        position:relative;
        display:inline-flex;
        align-items: center;
        padding-left:27px;
        font-size:16px;
        font-weight:700;

        small {
          font-weight:400;
          font-size:13px;
        }
      }

      .btn-charge {
        padding:3px 10px;
        line-height:1;
        border-radius:22px;

        .txt {
          padding-left:16px;
        }
      }

      .btn-season {
        font-weight:500;
        border-radius:24px;
      }

      .btt-flex {
        justify-content: space-between;
        font-size:14px;
        font-weight:700;
        color:rgba(51, 51, 51, 1);

        .btn-season {
          .ico.season {
            padding-left:25px;

            &::before {
              width:19px;
              height:11px;
              background-image:url('../images/icon/ticket_blue.svg')
            }
          }
        }

        .more {
          font-size:13px;
        }
      }
    }
  }

  .my-nav {
    padding:16px 0;
    & > li {
      width:33.33%;
      text-align: center;
      position:relative;

      &::after {
        content:"";
        display:block;
        width:1px;
        height:39px;
        background:rgba(215, 215, 215, 1);
        position:absolute;
        right:0;
        top:50%;
        transform: translate(0, -50%);
      }

      &:last-child {
        &::after {
          display:none;
        }
      }
    }
  }

  .sec-link {
    background:rgba(247, 247, 247, 1);

    .list-container {
      background: #fff;
      border-radius: 16px;
      padding:16px 24px;
    }
  }

  .bubble-list {
    padding-top:8px;

    .bubble-tab-container {
      padding-bottom:4px;
    }

    .card-flex > li {
      padding-bottom:12px;
      padding-top:12px;;
    }
  }

  .sec-reserve {
    background: #fff;
    padding:13px 0;

    .reserve-wrap {
      margin-top:16px;
      padding:0 24px;
    }

    .end-wrap {
      padding:24px;
      margin-top:4px;
      background:rgba(247, 247, 247, 1);
    }
  }

  &.t-termlist {
    .custom-list {
      .list-ul > li:last-child {
        border-bottom:1px solid rgba(233, 233, 233, 1);
      }
    }
  }

  &.t-term {

    .m-main {
      height:100%;
    }

    .term-wrap {
      height:calc(100% - 24px);
      background:rgba(247, 247, 247, 1);
      border-radius:16px;
      padding:16px;
      overflow: hidden;

      .edit-wrap {
        height:100%;
        overflow: hidden;
        overflow-y: auto;
      }
    }
  }

  .btt-tip {
    color:rgba(117, 117, 117, 1);
    border-top:1px solid rgba(233, 233, 233, 1);
    padding-top:9px;
    margin-top:24px;
    font-size:11px;
  }

  .sec-myinfo {
    background: #fff !important;
    padding-top:28px !important;

    .myinfo-container {
      .box {
        .add-img {
          width:80px;
          height:80px;
          margin:0 auto;
          border-radius: 100%;
          background:#fff url('../images/icon/logo symbol.svg') center no-repeat;
          background-size:42px auto;
          position:relative;

          &::after {
            content:"";
            display:block;
            width:16px;
            height:16px;
            border-radius: 100%;
            background:rgba(0, 35, 101, 1) url('../images/icon/icon_plus.svg') center no-repeat;
            background-size:8px;
            position:absolute;
            right:0;
            bottom:0;
          }
        }

        .blit-list {
          color:rgba(117, 117, 117, 1);
          font-size:11px;
          width:100%;
          max-width:300px;
          margin:0 auto;
          margin-top:20px;
        }
      }

      .dl-form {
        margin-top:30px;

        & > dt,
        & > dd {
          display:flex;
          align-content: center;
          justify-content: space-between;
        }

        & > dt {
          margin-bottom:16px;

          .name {
            font-size:16px;
          }
        }

        & > dd {
          .col-left {
            width:40px;
          }

          .col-right {
            width:calc(100% - 40px);
            padding-left:10px;

            [type=text] {
              height:40px;
            }
          }
        }
      }
    }

    .btt-btn {
      text-align: center;

      & > .btn-ul {
        display:inline-flex;
        align-content: center;
        margin:0 auto;

        & > li {
          margin:0;
          padding:0 15px;
          position:relative;

          &::after {
            content:"";
            width:1px;
            height:10px;
            background:rgb(117, 117, 117, 0.4);
            display:block;
            position:absolute;
            right:0;
            top:50%;
            transform: translate(0, -50%);
          }

          &:last-child {
            &::after {
              display:none;
            }
          }
        }
        .link-item {
          color: rgb(117, 117, 117);
          font-weight: 400;
          font-size:11px;
        }
      }
    }
  }

  &.t-cs {
    background:rgba(247, 247, 247, 1);

    .list-container {
      background:#fff;
      border-radius:18px;
      border:1px solid rgba(233, 233, 233, 1);
      padding:8px 24px;

      .custom-list {
        .list-ul > li {
          .item-flex {
            .item {
              padding:16px 0;
              font-size:14px;
              font-weight:400;
            }
          }
        }
      }
    }
  }

  &.t-report {
    background:rgba(247, 247, 247, 1);
  }

  &.t-shoot {
    .location-container {
      margin-bottom:15px;

      .title {
        color:rgba(0, 35, 101, 1);
        font-size:14px;
        font-weight:700;
        display:block;
        text-align: center;
        margin-bottom:11px;
      }

      .location-header {
        text-align: left;
        background:rgba(247, 247, 247, 1);

        .stadium {
          color:rgba(117, 117, 117, 1);
          font-size:12px;
        }

        .tip {
          font-size:10px;
          color:rgba(102, 102, 102, 1);
        }
      }
    }

    .score-setting {
      border-top:2px solid rgba(241, 241, 241, 1);
      padding:12px 35px;
      margin:0 -35px;

      .custom-list.setting {
        .inner > dd {
          padding:12px 0;

          .list-ul > li {
            .item-flex {
              height:auto;
              padding:12px 0;

              .addText {
                margin-top:5px;
              }
            }
          }
        }
      }
    }

    .vs-container {
      .vs-box {
        background:rgba(244, 248, 251, 1);
        border-radius:8px;
        padding:16px 10px;

        .top-flex {
          justify-content: center;
          position:relative;
          flex-wrap:nowrap;
          text-align: center;

          &::after,
          &::before {
            content:"";
            display:block;
            position:absolute;
            left:50%;
            transform: translate(-50%, 0);
          }

          &::after {
            content:"VS";
            color:rgba(100, 115, 199, 1);
            font-weight:700;
            top:10px;
          }

          &::before {
            content:":";
            color:rgba(102, 102, 102, 1);
            font-weight:700;
            bottom:10px;
          }

          & > li {
            padding:0 15px;

            & > dl {
              & > dt {
                margin-bottom:8px;

                small {
                  display:block;
                  color:rgba(153, 153, 153, 1);
                  font-size:11px;
                  font-weight:600;
                }

                .team {
                  color:rgba(51, 51, 51, 1);
                  font-weight:700;
                  margin-top:5px;
                }
              }

              & > dd {
                .box {
                  min-width:110px;
                  background:#fff;
                  font-size:16px;
                  font-weight:700;
                  color:rgba(102, 102, 102, 1);
                  line-height:1;
                  padding:11px 0;
                }
              }
            }
          }
        }
      }

      .tip {
        margin-top:16px;
        font-size:11px;
      }
    }
  }

  &.t-creatTeam {
    .top-tip {
      text-align: center;
      margin-bottom:20px;
      color:rgba(117, 117, 117, 1);
    }

    .form-container {
      background:rgba(247, 247, 247, 1);
      border-radius: 16px;
      padding:24px 16px;

      .form-group {
        margin-bottom:16px;

        &:last-child {
          margin-bottom:0;
        }
      }
    }

    .blit-list {
      font-size:11px;
      color:rgba(117, 117, 117, 1);
    }
  }

  &.t-balluse {
    background:rgba(247, 247, 247, 1);

    .sec1 {
      background:#fff;

      .box {
        justify-content: space-between;
        padding:20px 28px;
        font-size:15px;

        & > dt {
          font-weight:500;
        }

        & > dd {
          font-weight:400;

          strong {
            font-weight:700;
            font-size:16px;
          }
        }
      }

      .extinction {
        justify-content: space-between;
        font-size:14px;
        border-bottom:1px solid rgba(233, 233, 233, 1);
        margin-top:17px;
        padding-bottom:8px;

        & > dt {
          color:rgba(153, 153, 153, 1);
          font-weight:600;
        }

        & > dd {
          color:rgba(51, 51, 51, 1);
          font-weight:700;
        }
      }
    }

    .sec2 {
      .use-container {
        background:#fff;
        border-radius: 16px;
        padding:16px 16px 32px;
      }
    }
  }

  &.t-charge {
    background:rgba(247, 247, 247, 1);

    .form-group {
      margin-bottom:24px;

      &:last-child {
        margin-bottom:0;
      }


      .form-wrap {
        margin-bottom:8px;

        &:last-child {
          margin-bottom:0;
        }
      }
    }

    .btt-btn {
      .tip-dl {
        color:rgba(117, 117, 117, 1);
        margin-bottom:17px;

        & > dt {
          font-weight:700;
          margin-bottom:12px;
        }

        .blit-list {
          font-size:12px;
        }
      }
    }
  }

  &.t-withdraw {
    .noti {
      font-size:18px;
      display:block;
      font-weight:700;
      padding-bottom:10px;
    }

    .over-wrap {
      padding:18px 20px;
      overflow: hidden;
      height:127px;

      .dec-wrap {
        color:rgba(117, 117, 117, 1);
        font-size:12px;
        height:100%;
        overflow: hidden;
        overflow-y: auto;
      }
    }

    .detail-box {
      margin-top:16px;
      padding:11px 24px;

      .top-flex,
      .btt-flex {
        justify-content: space-between;
        line-height:1;

        & > dt {
          color:rgba(117, 117, 117, 1);
          font-size:11px;
          font-weight:600;
        }

        & > dd {
          color:rgba(51, 51, 51, 1);
          font-size:11px;
        }
      }

      .top-flex {
        border-bottom: 1px solid rgba(233, 233, 233, 1);
        padding-bottom:10px;
        margin-bottom:8px;

        & > dd {
          .ball {
            font-size:16px;
            font-weight:700;
          }
        }
      }
    }

    .form-container {
      margin-top:24px;

      .form-wrap {
        flex-wrap:wrap;
      }

      .chk-label {
        width:100%;
        margin-bottom:12px;

        &:last-child {
          margin-bottom:0;
        }

        .txt {
          font-size:13px;
        }
      }
    }

    .form-box {
      margin-top:16px;
      padding:16px;

      textarea {
        height:100px;
      }

      .btt-flex {
        margin-top:9px;
        justify-content: flex-end;
        font-size:11px;
      }

    }

    .btt-btn {
      margin-top:40px;

      .chk-label {
        .txt {
          color:rgba(117, 117, 117, 1);
          font-size:13px;

          &::before {
            top:4px;
            transform: translate(0, 0);
          }
        }
      }

      .btn-ul {
        margin-top:16px;
      }
    }
  }

  &.t-interest {
    .top-tip {
      text-align: center;
      margin-bottom:24px;
    }

    .form-container {
      .form-group {
        margin-bottom:32px;

        &:last-child {
          margin-bottom:0;
        }
      }

      .col-form {
        padding:24px;
        border:1px solid rgba(233, 233, 233, 1);
        background:rgba(247, 247, 247, 1);
        border-radius: 16px;

        .form-wrap {
          flex-wrap:wrap;
          gap:0;
        }

        .chk-label {
          width:100%;
          margin-bottom:16px;
          margin-right:0;

          &:last-child {
            margin-bottom:0;
          }
        }
      }

      &._drag {
        .col-form {
          background:#fff;
        }

        .chk-label {
          padding-bottom:10px;
          margin-bottom:10px;
          border-bottom:1px dashed rgba(233, 233, 233, 1);
          background:url('../images/icon/btn_menu.svg') no-repeat center right;
          background-size:14px auto;

          &:last-child {
            padding-bottom:0;
            margin-bottom:0;
            border-bottom:0;
          }
        }
      }
    }
  }

  &.t-history {
    .tab-container {
      .tabs {
        margin-top:0;
      }

      .tab-content {
        padding-top:16px;
      }

      .top-flex {
        display:flex;
        justify-content: space-between;

        .selectLayer {
          border:1px solid rgba(233, 233, 233, 1);
          background:rgba(247, 247, 247, 1);
          color:rgba(51, 51, 51, 1);
          padding:3px 11px;

          .txt {
            padding-right:15px;
          }
        }
      }
    }
  }

  &.t-season {
    .season-list {
      & > li {
        margin-bottom:16px;

        &:last-child {
          margin-bottom:0;
        }
      }

      .link-item {
        display: block;
        width:100%;
      }
    }

    .article {
      padding-bottom:30px;
      margin-bottom:18px;
      border-bottom:1px solid rgba(233, 233, 233, 1);
    }
  }

  .bbs-list {
    & > li {
      .item-flex {
        padding:16px 0;
      }
    }
  }
}

@media screen and (max-width: 860px) {
  .page-my {
    .my-flex {
      margin:0 -5px;

      .col-summary,
      & > .col-right {
        padding:0 5px;
        width:310px;
      }

      & > .col-right {
        width:calc(100% - 310px);
      }

      .right-outer {
        padding:38px 15px;

        .bg-box {
          padding:20px 15px;
        }

        & > .bg-box {
          height:100%;
          padding:0;
        }

        .sec:not(.full-height),
        .sec2 {
          background:#fff;
        }

        &.t2 {
          .sec-wrap,
          .full-height {
            margin:0 -15px;
          }

          .full-height {
            padding-top:24px;

            & > .m-main {
              padding:0 15px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .page-my {
    .my-flex {
      .col-summary {
        display:none;
      }

      & > .col-right {
        width:100%;
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .page-my {
    .my-flex {
      .right-outer {
        .card-flex > li {
          width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .page-my {
    min-height:calc(100vh - 36px);

    &:not(.bg-gray) {
      background: #fff;
    }

    & > .m-main {
      padding:0
    }

    .my-flex {
      & > .col-right {

      }

      &:not(._full) {
        padding:0;
      }

      .right-outer {
        background:transparent;
        overflow:initial;
        border-radius: 0;
        padding:0;
        box-shadow: none;

        .m-main {
          padding:0 15px;
        }

        .right-header {
          display:none;
        }

        .sec,
        .sec2 {
          & > .bg-box {
            padding-left:0;
            padding-right:0;
          }
        }

        .full-height {
          .col-top {
            background:transparent;

            .top-first {
              padding-top:0;
            }

            & > .bg-box {
              padding:0;
            }
          }

          .btt-btn {
            & > .bg-box {
              padding:0;
            }
          }
        }

        &.t2 {
          .sec-wrap,
          .full-height {
            margin:0;
          }
        }

        &.t3 {
          .full-height {
            .col-top {
              .noti {
                br {
                  display:block;
                }
              }
            }

            .btt-btn {
              .chk-label {
                .txt,
                .txt > * {
                  white-space: pre-wrap;
                }
              }
            }
          }
        }
      }
    }

    .sec-reserve {
      .reserve-wrap {
        padding:0;
      }

      .end-wrap {
        padding:24px 0;
      }
    }

    &.t-shoot {
      .score-setting {
        padding:12px 0;
        margin:0 -15px;
      }

      .vs-container {
        .vs-box {
          background:rgba(244, 248, 251, 1);
          border-radius:8px;
          padding:16px 10px;

          .top-flex {
            justify-content: center;
            position:relative;
            flex-wrap:nowrap;
            text-align: center;

            &::after,
            &::before {
              content:"";
              display:block;
              position:absolute;
              left:50%;
              transform: translate(-50%, 0);
            }

            &::after {
              content:"VS";
              color:rgba(100, 115, 199, 1);
              font-weight:700;
              top:10px;
            }

            &::before {
              content:":";
              color:rgba(102, 102, 102, 1);
              font-weight:700;
              bottom:10px;
            }

            & > li {
              padding:0 15px;

              & > dl {
                & > dt {
                  margin-bottom:8px;

                  small {
                    display:block;
                    color:rgba(153, 153, 153, 1);
                    font-size:11px;
                    font-weight:600;
                  }

                  .team {
                    color:rgba(51, 51, 51, 1);
                    font-weight:700;
                    margin-top:5px;
                  }
                }

                & > dd {
                  .box {
                    min-width:110px;
                    background:#fff;
                    font-size:16px;
                    font-weight:700;
                    color:rgba(102, 102, 102, 1);
                    line-height:1;
                    padding:11px 0;
                  }
                }
              }
            }
          }
        }

        .tip {
          margin-top:16px;
          font-size:11px;
        }
      }
    }
  }
}

.use-list {
  & > li {
    border-bottom:1px solid rgba(233, 233, 233, 1);
    padding:16px 0;

    & > dl {
      padding:0 10px;

      & > dt {
        padding:8px 0;
        justify-content: space-between;
        color:rgba(51, 51, 51, 1);
        font-size:14px;
        font-weight:700;

        .tit {
          width:calc(100% - 55px);
          padding-right:5px;
        }

        .ball {
          width:55px;
          text-align: right;
        }
      }

      & > dd {
        font-size:12px;
        color:rgba(102, 102, 102, 1);
      }
    }
  }
}

.notice-container {
  .notice-list {
    padding:14px 0;

    .link-item {
      position:relative;
      display:flex;
      justify-content: space-between;
      padding-left:23px;
      color:rgba(51, 51, 51, 1);

      &::before {
        content:"";
        display:inline-block;
        width:18px;
        height:14px;
        background:url('../images/icon/icon_my_vol.svg') center no-repeat;
        background-size:cover;
        position:absolute;
        left:0;
        top:50%;
        transform:translate(0, -50%);
      }

      .ellip {
        width:calc(100% - 65px);
        padding-right:10px;
      }

      .date {
        width:65px;
        color:rgba(153, 153, 153, 1);
      }
    }
  }
}

.page-footer  {
  text-align: center;
  padding:20px 10px;

  dt {
    font-weight: 700;

    .btn-drop {
      font-size:12px;
      text-decoration: none;
      color:rgba(102, 102, 102, 1);
    }

    .drop-wrap {
      margin-bottom:20px;
      font-weight:400;
      font-size:10px;
      color:rgba(117, 117, 117, 1);

      dl {
        display:inline-flex;
        padding:0 5px;

        dt {
          font-weight:400;
          white-space: nowrap;

          &::after {
            content:":";
            margin:0 3px;
          }
        }
      }
    }
  }

  dd {
    font-size:10px;
  }
}

.season-ticket {
  border:1px solid rgba(233, 233, 233, 1);
  background: #fff;
  border-radius: 16px;
  padding:18px 24px;
  text-align:left;

  & > dt {
    display:flex;
    justify-content: space-between;
    font-size:15px;
    font-weight:700;
    line-height:1;
    padding-bottom:9px;
    margin-bottom:9px;
    border-bottom:1px solid rgba(233, 233, 233, 1);
    color:rgba(51, 51, 51, 1);
  }

  & > dd {
    color:rgba(117, 117, 117, 1);
    font-size:14px;
    white-space: pre-wrap;

    .tip {
      color:rgba(255, 59, 48, 1);
      font-size:11px;
    }
  }

  .inner-dl {
    display:flex;
    justify-content: space-between;
    color:rgba(51, 51, 51, 1);
    margin-bottom:12px;

    & > dt {
      font-weight:600;
    }

    & > dd {
      font-weight:700;
    }
  }

  .date-dl {
    color:rgba(0, 35, 101, 1);
    font-size:14px;
    font-weight:600;

    & > dt,
    & > dd {
      display:inline-block;
      font-weight:600;
    }

    & > dt {
      &::after {
        content:":";
        display:inline-block;
        margin:0 3px;
      }
    }
  }

  &.small {
    & > dt {
      font-size:12px;
    }

    & > dd {
      font-size:11px;
    }
  }
}