.req {
  position: relative;

  &:after {
    content: "*";
    display: inline-block;
    margin-left: 3px;
    color: rgb(255, 77, 79);
  }
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

label.btn {
  margin: 0;
}

.checkbox.checkbox-css.disabled {
  opacity: 0.5;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* label, input */
div.checkbox label,
div.radio label {
  cursor: pointer;
}

.radio.radio-css,
.checkbox.checkbox-css {
  margin-right: 15px;
}

/* check-box */
.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  gap: 8px;
}

.checkbox input {
  appearance: none;
  box-sizing: border-box;
  border: 1px solid #bdbdbd;
  cursor: pointer;
}

.checkbox label {
  color: #000000;
}

.checkbox label a {
  color: #000000;
}

.checkbox.main-checkbox {
  margin-bottom: 24px;
  transform: translateX(-4px);
}

.checkbox.square.small input {
  width: 20px;
  height: 20px;
  border-radius: 2px;
  background-color: white;
}

.checkbox.square.small label,
.checkbox.circle.small label {
  font-size: 0.8125rem;
}

.checkbox.square.large input {
  width: 24px;
  height: 24px;
  border-radius: 2px;
  background-color: white;
}

.checkbox.square.large label,
.checkbox.circle.large label {
  font-size: 1rem;
  font-weight: 700;
}

.checkbox.square.small input:checked,
.checkbox.square.large input:checked {
  border-color: transparent;
  background-color: #304ffe;
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='white' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox.square.small input:not(:checked),
.checkbox.square.large input:not(:checked) {
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='gainsboro' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='gainsboro' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox.circle.small input {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
}

.checkbox.circle.large input {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: white;
}

.checkbox.circle.small input:checked,
.checkbox.circle.large input:checked {
  border-color: transparent;
  background-color: #304ffe;
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='white' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox.circle.small input:not(:checked),
.checkbox.circle.large input:not(:checked) {
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='gainsboro' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='gainsboro' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox label {
  margin: 0;
}

