.form-group {
  align-items: stretch;

  &:last-child {
    margin-bottom: 0;
  }

  .col-form {
    width: calc(100% - 70px);
  }

  .col-form-label {
    height: 40px;
    line-height: 1;
    padding: 0;
    display: flex;
    align-items: center;
    width: 70px;
    font-size: 13px;
    font-weight: 600;
    color: #333;

    &.nolabel {
      display: none;
    }
  }

  .form-wrap {
    position: relative;
    display: flex;
    white-space: nowrap;
    flex-wrap: wrap;
    border: 1px solid #ced4da;
    /*border-radius: 0.25rem;*/
    border-radius: 8px;
    overflow: hidden;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

    .length {
      font-size: 0.75rem;
      color: #999;

      /*&.bottom {
        right:0;
        top:calc(100% + 20px)
      }*/
    }

    &.rangepicker {
      border: none;
      width: 100%;
      display: flex;
      justify-content: center;
      overflow: visible;
    }

    &.p {
      border: 0;

      .btn-flex {
        display: none;
      }

      .dec {
        padding: 10px 0;
      }
    }

    &.out {
      border: 0;
      padding-right: 35px;

      [type="text"],
      [type="password"],
      select,
      textarea {
        border: 1px solid #ced4da;
        border-radius: 8px;
      }

      .btn-flex {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
      }

      .length {
        position: absolute;
        right: 50px;
        top: 50%;
        transform: translate(0, -50%);
      }
    }

    &.checkbox,
    &.radio {
      padding: 6px 0;
      border: 0;
      border-radius: 0;
    }

    /*&._write {
      padding-right: 40px;

      .btn-flex {
        & > .col-del {
          display: block;
        }
      }
    }*/

    &._readOnly {
      padding-right: 0;

      .btn-flex {
        & > .col-del {
          display: none;
        }
      }
    }

    &._round {
      border-radius: 40px;
      overflow: hidden;
    }

    &._readonly {
      padding-right: 0;

      input {
        background-color: #e9ecef !important;
      }
    }

    &._remove,
    &._auth {
      border: 0;
      border-radius: 0;
      padding-right: 30px;

      [type="text"],
      [type="password"],
      select,
      textarea {
        border: 1px solid #ced4da !important;
        height: 40px;
      }

      .btn-flex {
        right: 0;
      }
    }

    &._auth {
      padding-right: 69px;
    }

    &.select {
      margin-top: 0;
    }

    select,
    input[type="text"],
    input[type="password"] {
      border: 0;
      width: 100%;
      height: 38px;
      border-radius: 8px;
    }

    .btn-flex {
      display: inline-flex;
      position: absolute;
      right: 15px;
      top: 50%;
      transform: translateY(-50%);

      & > li {
        padding: 0 5px;
        display: flex;
        align-items: center;

        /*&.col-del {
          display: none;
        }*/

        &:last-child {
          padding-right: 0;
        }
      }

      .ico-btn {
        display: block;
        width: 15px;
        height: 15px;
        padding: 0;
        background-size: cover;

        &.btn-del {
          background-image: url(../images/icon/icon_del.svg);
        }

        &.btn-search {
          background-image: url(../images/icon/icon_search.svg);
        }

        &.btn-edit {
          background-image: url(../images/icon/icon_edit.svg);
        }

        &.btn-edit2 {
          width: 25px;
          height: 25px;
          background-image: url(../images/icon/icon_update.svg);
        }

        &.btn-preview {
          background-image: url(../images/icon/icon_pwview.svg);
        }

        &.ico-check {
          background-image: url(../images/icon/icon_chked.svg);
          width: 12px;
          height: 9px;
        }

        &.btn-remove {
          background-image: url(../images/icon/icon_remove.svg);
          width: 20px;
          height: 20px;
        }

        &.btn-auth {
          border-radius: 0.25rem;
          width: 60px !important;
          height: 40px !important;
          line-height: 38px !important;
          text-align: center;
          border: 1px solid #333;
          background: #333;
          color: #fff;
          font-size: 12px;
        }

        &.disabled {
          border-radius: 0.25rem;
          height: 40px;
          line-height: 38px;
          text-align: center;
          width: 60px;
          color: #fff;
          font-size: 12px;
          border: none;
          background: #999999;
        }
      }
    }

    textarea {
      min-height: 100px;
      border: 0;
      border-radius: 8px;
      resize: none;
    }

    select {
      border: 0;
    }

    .innerWrapper {
      width: 100%;
      display: flex;
      white-space: nowrap;
    }

    &.imgUpload {
      border: 0;
      border-radius: 0;
      display: block;
      overflow: initial;
    }

    &._arrow {
      overflow: initial;

      .rdt {
        width: 100%;
      }
    }

    &.button {
      .btn {
        width:100%;
        text-align: left;
        font-weight:400 !important;
        background:#fff;
        padding:12px 10px;
        line-height:1;
        font-size:13px !important;
        color:#8c939a !important;
      }
    }
  }

  &._w100 {
    & > * {
      width: 100% !important;
    }
  }

  .tip {
    margin-top: 9px;
    font-size: 11px;
  }

  [type="text"],
  [type="password"],
  select {
    padding-left: 10px;
    padding-right: 10px;
  }

  textarea {
    padding: 10px;
    width: 100%;
  }
}

.tip-flex {
  margin-top: 9px;
  justify-content: space-between;
}

// checkbox
.chk-label {
  display: inline-block;
  position: relative;
  margin-right: 10px;

  &:last-child {
    margin-right: 0;
  }

  input {
    border: 0;
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 1px;
    opacity: 0;
  }

  label {
    margin-bottom: 0;
  }

  & > .txt,
  & > label > .txt {
    display: block;
    position: relative;
    padding-left: 22px;
    font-size: 12px;
    margin-bottom: 0;

    &::before,
    &::after {
      content: "";
      display: block;
      position: absolute;
      background-size: cover;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
    }

    .area {
      display: inline-block;
      padding: 0 10px;
      line-height: 20px;
      font-size: 12px;
      font-weight: 600;
      color: #fff;
      border-radius: 20px;
      background: rgba(117, 159, 248, 1);
      margin-right: 6px;
    }

    .team {
      font-size: 13px;
    }
  }

  &.radio {
    & > .txt,
    & > label > .txt {
      padding-left: 18px;

      &::before {
        content: "";
        width: 14px;
        height: 14px;
        background: #ccc;
        border-radius: 100%;
      }
      &::after {
        content: "";
        width: 6px;
        height: 6px;
        background: #fff;
        border-radius: 100%;
        left: 4px;
      }
    }

    input:checked + .txt {
      &::before {
        background: #304ffe;
      }
    }

    &.big {
      .txt {
        padding-left: 22px;

        &::before {
          content: "";
          width: 18px;
          height: 18px;
          background: #ccc;
          border-radius: 100%;
        }
        &::after {
          content: "";
          width: 7px;
          height: 7px;
          background: #fff;
          border-radius: 100%;
          left: 5px;
        }
      }

      input:checked + .txt {
        &::before {
          background: #304ffe;
        }
      }
    }
  }

  &.checkbox {
    & > .txt,
    & > label > .txt {
      padding: 5px 0;
      padding-left: 28px;
      white-space: pre-wrap;

      &::before {
        content: "";
        width: 20px;
        height: 20px;
        background: #fff url("../images/icon/icon_chked_gray.svg") center
          no-repeat;
        background-size: 11px 8px;
        border-radius: 2px;
        border: 1px solid #bdbdbd;
      }
    }

    input:checked + .txt {
      &::before {
        border-color: #304ffe;
        background-color: #304ffe;
        background-image: url("../images/icon/icon_chked_white.svg");
      }
    }

    &.round {
      .txt {
        &::before {
          border-radius: 100%;
        }
      }
    }

    &.big {
      .txt {
        padding-left: 28px;

        &::before {
          width: 24px;
          height: 24px;
          background-size: 13px 9px;
        }
      }
    }
  }

  &.none {
    & > .txt,
    & > label > .txt {
      padding-left: 0;
      padding-right: 18px;

      &::after {
        display: none;
      }
      &::before {
        content: "";
        display: none;
        border: 0;
        border-radius: 0;
        width: 12px;
        height: 9px;
        background-size: cover;
        left: auto;
        right: 0;
      }
    }

    input:checked + .txt {
      color: #304ffe;
      font-weight: 700;

      &::before {
        display: block;
        background: transparent url("../images/icon/Icon_chked_blue.svg") center
          no-repeat;
      }
    }
  }

  &.t-btn {
    label {
      display: inline-block;
      color: #333;
      background: #fff;
      border: 1px solid #333;
      border-radius: 32px;
      padding: 6px 15px;
      font-weight: 600;

      &::before {
        display: none;
      }

      .txt {
        padding-left: 0;
        padding-right: 16px;

        &::before {
          content: "";
          width: 12px;
          height: 8px;
          background: url("../images/icon/icon_arrow_down_black.svg") center
            no-repeat;
          background-size: 100% auto;
          right: 0;
          left: auto;
        }
      }
    }

    input:checked + label {
      color: #fff;
      background: #333;

      &::before {
        display: none;
      }

      .txt {
        &::before {
          background-image: url("../images/icon/icon_arrow_down.svg");
        }
      }
    }

    &.round {
      .txt {
        &::before {
          border-radius: 100%;
        }
      }
    }

    &.big {
      .txt {
        padding-left: 34px;

        &::before {
          width: 24px;
          height: 24px;
          background-size: 13px 9px;
        }
      }
    }

    &.square {
      label {
        border-radius: 4px;
        padding: 10px;
        text-align: center;
        color: rgba(0, 0, 0, 1);
        border-color: rgba(233, 233, 233, 1);
      }

      input:checked + label {
        color: #fff;
        background: rgba(48, 79, 254, 1);
        border-color: rgba(48, 79, 254, 1);
      }
    }
  }

  &.type-btn {
    .txt {
      padding: 5px 20px !important;
      border-radius: 27px !important;
      border: 1px solid rgba(233, 233, 233, 1) !important;
      color: rgba(51, 51, 51, 1) !important;
      font-size: 12px !important;

      &::before,
      &::after {
        display: none;
      }
    }

    :checked + .txt {
      border-color: rgba(178, 188, 244, 1) !important;
      background: rgba(235, 240, 255, 1) !important;
      color: rgba(0, 35, 101, 1) !important;
    }
  }
}

