          width: 110px;
          padding-right: 20px;

          & > li {
            margin-bottom: 8px;

            &:last-child {
              margin-bottom: 0;
            }
          }

          .tab {
            width: 100%;
            text-align: center;
            border: 1px solid rgba(233, 233, 233, 1);
            font-size: 12px;
            padding: 5px;
            margin-bottom: 8px;
            border-radius: 8px;

            &.active {
              border-color: rgba(178, 188, 244, 1);
              background: rgba(235, 240, 255, 1);
              color: rgba(0, 35, 101, 1);
            }
          }
        }

        .tab-content {
          width: calc(100% - 110px);
          padding-left: 20px;
        }

        .districts-flex {
          margin: 0 -10px;

          & > li {
            padding: 0 10px;
            margin-bottom: 8px;
            width: 50%;

            .chk-label {
              margin: 0;

              .txt {
                border: 1px solid rgba(233, 233, 233, 1);
                font-size: 12px;
                padding: 5px;
                margin-bottom: 8px;
                border-radius: 8px;
                text-align: center;
              }

              :checked + .txt {
                border-color: rgba(178, 188, 244, 1);
                background: rgba(235, 240, 255, 1);
                color: rgba(0, 35, 101, 1);

                &::after {
                  display:none;
                }
              }
            }
          }
        }
      }
    }
  }
}

.tag-select {
  .select-content {
    .list-item {
      display: flex;

      &.drop-wrap {
        .chk-label {
          line-height: 1;
          width: calc(100% - 40px);
          margin: 0;
        }

        .link-item {
          width: 40px;
          text-align: right;
        }
      }
    }

    ._viewall {
      .chk-label {
        width: 100% !important;
      }

      .link-item {
        display: none;
      }

      .tag-list {
        display: block;
      }
    }
  }

  ._disabled {
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
    }

    .chk-label .txt,
    .link-item.more {
      opacity: 0.5;
    }
  }

  .tag-list {
    padding-top: 22px;
    display: none;

    & > li {
      margin-bottom: 22px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

// range-picker
.range-picker {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  // padding: 6px;

  .col-form-label {
    width: 100%;
  }
  .range-wrapper {
    width: 100%;
    .range-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .date-picker {
        width: 45%;

        input {
          border: 1px solid #ced4da;
          border-radius: 8px;
        }

        .rdtPicker {
          table {
            table-layout: auto !important;
          }
        }
      }
    }
  }
}

.upload-flex {
  margin: 0 -5px;

  & > li {
    padding: 0 5px;
    width: 25%;
  }

  &.round {
    .upload-box {
      border-radius: 100%;
      border: 0;
      background: #fff url("../images/icon/logo_symbol_mint.svg") center
        no-repeat;
      background-size: 46px auto;

      .icon {
        background: none;
        width: 16px;
        height: 16px;
        left: auto;
        top: auto;
        right: 3px;
        bottom: 6px;
        transform: translate(0, 0);

        &::after {
          right: 0;
          bottom: 0;
        }
      }
    }
  }

  &._single {
    & > li {
      width: 80px;
    }
  }
}

.upload-box {
  width: 100%;
  padding-top: 100%;
  border: 1px solid rgba(233, 233, 233, 1);
  border-radius: 4px;
  background-color: #fff;
  position: relative;
  height: 0;

  &.form {
    .icon {
      display: block;
      width: 29px;
      height: 22px;
      background: url(../images/icon/icon_camera.svg) center no-repeat;
      background-size: cover;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

      &::after {
        content: "";
        display: block;
        width: 16px;
        height: 16px;
        border-radius: 100%;
        background: rgba(0, 35, 101, 1) url(../images/icon/icon_plus.svg) center
          no-repeat;
        background-size: 8px;
        position: absolute;
        right: -7px;
        bottom: -6px;
      }
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
    }
  }

  .img-box {
    border-radius: 4px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  .btn-remove2 {
    position: absolute;
    right: -5px;
    top: -7px;
  }
}

.rdtPicker {
  table {
    table-layout: initial;
  }
}
