//page-landing
.page-landing {
  padding-bottom:40px;
}

.vod-layout {
  padding-top:24px;
  margin:0 -14px;
  align-items:stretch;

  .vod-left,
  .vod-tab {
    padding:0 14px;
  }

  .vod-left {
    width:calc(100% - 384px);

    .box-wrap {
      background:#F7F7F7;
      border-radius:12px;
      padding:16px;
      height:100%;
    }
  }

  .vod-tab {
    width:384px;
    height:750px;

    .sec-tab,
    .tab-container,
    .tabs {
      height:100%;
    }

    .sec-tab {
      .tab-content {
        height:calc(100% - 44px);

        .tab-item {
          height:100%;
          padding:0;
          position:relative;

          .content-item {
            height:100%;
            margin:0 -16px;
            padding:10px 16px;
            overflow: hidden;
            overflow-y: auto;
          }

          &.clip,
          &.tag {
            .content-item {
              height:calc(100% - 45px);
            }
          }
        }
      }
    }

    .btt-btn {
      &.t1 {
        margin: 0;
        padding: 0;
        width: 100%;
        padding-top: 5px;
        position: absolute;
        bottom: 0;
      }
    }
  }
}

//sec-vod
.sec-vod {
  position:relative;
}

.vod-container {
  position:relative;
  background:#000;
  display:flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  overflow: hidden;

  &._dim {
    &::before {
      content:"";
      display:block;
      background:rgba(0, 0, 0, 0.6);
      width:100%;
      height:100%;
      position:absolute;
      left:0;
      top:0;
    }
  }

  .btn,
  .middle-txt {
    z-index:1;
  }

  .middle-txt {
    width:100%;
    max-width:300px;
    display:flex;
    justify-content:space-between;
    position:absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    white-space: nowrap;

    .small {
      font-size:16px;
    }
  }

  .tit {
    color:#fff;
    font-size:32px;
    font-weight:700;
    position:absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
  }

  .ico-back {
    position:absolute;
    left:15px;
    top:6px;
    background-size:14px 24px;
    width: 14px !important;
    height: 24px !important;

    .txt {
      display:none;
    }
  }

  .ico-close {
    position:absolute;
    left:15px;
    top:6px;
  }

  .ico-ul {
    position:absolute;
    display:flex;

    & > li {
      margin-right:13px;

      &:last-child {
        margin-right:0;
      }
    }
  }

  .top-btn {
    top:14px;
    right:16px;
  }

  .btt-btn {
    bottom:9px;
    right:16px;
  }

  .right-btn-container {
    position:absolute;
    display:none;
    align-items:center;
    width:25px;
    top:0;
    bottom:0;
    right:20px;

    .right-btn {
      width:100%;
      display:flex;
      flex-wrap:wrap;

      & > li {
        width:100%;
        margin:0;
        margin-bottom:25px;

        .width-txt {
          background-position:top center;
          text-align: center;
        }
      }
    }
  }

  .video {
    width:100%;
    max-width:1008px;
  }

  .play-btn {
    display:inline-flex;
    gap:57px;
    position:absolute;
    left:50%;
    top:50%;
    transform: translate(-50%, -50%);
    z-index:2;

    & > li {
      width:50px;
      height:50px;
      position:relative;

      .btn {
        position:absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
      }

      .vod-prev {
        display:flex;
        align-items:center;
        justify-content:center;
        padding:0;
        width:50px;
        height:50px;
        color:#fff;
        font-size:14px;
        border-radius: 100%;
        background:rgba(217, 217, 217, 0.2);
      }
    }
  }

  // 전체영상
  &.full {
    width:100%;
    height:100% !important;
    position:fixed;
    left:0;
    top:0;
    z-index:300;
    border-radius: 0;

    .video {
      max-width:100vw;
      max-height:100vh;
      object-fit:contain;
    }

    .ico-back {
      width: auto !important;
      padding-left:20px;

      .txt {
        display:block;
      }
    }

    .top-btn {
      .ico-tag {
        display:none;
      }
    }

    .right-btn-container {
      display:flex;
    }

    .multi-layer {
      width:35%;
      height:100%;
      padding:50px 12px;
      left:auto;
      right:0;

      .thumb-list {
        flex-wrap:wrap;
        height:100%;

        & > li {
          width:100%;
          margin:0;
          margin-bottom:16px;

          dl {
            display:flex;
            flex-direction: row-reverse;

            dt {
              width:65%;
              padding-left:8px;
              display:flex;
              flex-wrap:wrap;
              align-items: center;

              .top {
                display:block;
                width:100%;
              }
            }

            dd {
              width:35%;
            }
          }
        }
      }

      .ico-down {
        top:17px;
        right:auto;
        left:9px;
        width:24px;
        height:14px;
        transform: rotate(-90deg);
      }
    }

    .clip-layer {
      padding-top:17px;
      padding-bottom:17px;

      .over-wrap {
        width:100%;
        overflow: hidden;
        overflow-x: auto;
      }

      .thumb-list {
        .thumb {
          width:70px;
        }
      }
    }
  }
}

.multi-layer,
.clip-layer,
.tag-layer {
  width:100%;
  background:rgba(0, 0, 0, 0.7);
  padding:10px 18px;
  position:absolute;
  left:0;
  bottom:0;
  z-index:200;

  .thumb-list {
    display:flex;
    overflow-x:auto;

    & > li {
      margin-right:10px;
      width:50px;

      &:last-child {
        margin-right:0;
      }

      dl {
        dt {
          color:#fff;
          font-size:10px;

          .top {
            display:none;
          }
        }
      }
    }

    .thumb {
      padding-top:66%;
      border:1px solid #808080;
      position:relative;

      &.play {
        &:before {
          content:'';
          display:block;
          width:100%;
          height:100%;
          position:absolute;
          top:0;
          left:0;
          background:rgba(0, 0, 0, 0.6);
        }

        &:after {
          content:'재생중';
          white-space: nowrap;
          color:#fff;
          font-size:10px;
          font-weight:600;
          display:block;
          background: url('../images/icon/btn_play.svg') top center no-repeat;
          padding-top:10px;
          background-size:8px;
          position:absolute;
          top:50%;
          left:50%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
}

.multi-layer {
  padding-right:50px;

  .ico-down {
    position:absolute;
    top:20px;
    right:17px;
  }
}

.clip-layer {
  padding:3px;
  z-index: 10;

  .over-wrap {
    line-height:0;
    overflow-x: auto;
  }

  .thumb-list {
    display:inline-flex;
    padding:7px 14px;

    .chk-label {
      margin-right:5px;

      &:last-child {
        margin-right:0;
      }

      .label {
        margin:0;
        display:block;
      }

      :checked + .label {
        &::before {
          content:'';
          display:block;
          width:calc(100% + 28px);
          height:calc(100% + 14px);
          border:3px solid #81EB47;
          border-left-width:11px;
          border-right-width:11px;
          border-radius: 4px;
          position:absolute;
          left:50%;
          top:50%;
          transform:translate(-50%, -50%);
          z-index:1;
        }
      }
    }

    .thumb {
      width:42px;
      padding-top:66%;
      border:0;
      border-radius: 4px;
    }
  }
}

.tag-layer {
  z-index:1;
  width:40%;
  height:100%;
  padding:10px 19px;
  left:auto;
  right:0;

  .flex-top {
    justify-content: space-between;
    color:#fff;
    font-weight:700;
  }

  .tag-list {
    margin-top:19px;
    height: calc(100% - 40px);
    overflow-y: auto;

    & > li {
      width:100%;
      margin-bottom:15px;

      &:last-child {
        margin-bottom:0;
      }
    }
  }
}

.tag-prograss {
  z-index:1;
  width:100%;
  height:4px;
  background:#FC3331;
  position:absolute;
  left:0;
  bottom:0;

  .mark {
    display:block;
    width:13px;
    height:18px;
    background-color:transparent;
    background-position: center;
    background-size: cover;
    position:absolute;
    bottom:8px;

    &.red {
      background-image: url(../images/icon/mark_red.svg);
    }

    &.blue {
      background-image: url(../images/icon/mark_blue.svg);
    }

    &.purple {
      background-image: url(../images/icon/mark_purple.svg);
    }
  }
}

//sec-info
.sec-info {
  margin-top:12px;

  .tag-wrap {
    margin-top:0;
    display:inline-flex;

    & > li {
      margin-right:5px;

      a {
        line-height:1;
        color:#757575;
      }
    }
  }
}

.info-container {
  & > .m-main {
    padding:0;
  }

  .tit-header {
    justify-content: space-between;
    align-items: flex-start;
    padding:16px 0;

    .top-flex,
    .btt-flex {
      justify-content: space-between;
    }
    .top-flex {
      padding-bottom:8px;
      margin-bottom:6px;
      border-bottom:1px solid rgba(213, 213, 213, 1);
    }

    .col-tit {
      .tit {
        font-size:15px;
        font-weight:600;
      }
    }

    .col-right {
      display:flex;
      justify-content: flex-end;
      flex-wrap:wrap;

      .dec {
        width:100%;
        text-align: right;
      }
    }

    .ico-ul {
      display:flex;

      & > li {
        margin-right:10px;

        &:last-child {
          margin-right:0;
        }
      }
    }
  }

  .tag-container {
    display:none;
    background:#F7F7F7;
    padding:8px;

    .top-flex {
      justify-content: space-between;

      .btn-ul {
        display:flex;
        gap:4px;
      }
      .col-right {
        font-size:12px;
      }
    }

    .col-right {
      color:#304FFE;
    }
  }
}

.card-container {
  background:#F7F7F7;
  /*padding:7px 0 16px;*/

  .m-main {
    padding:0;
  }
}

.tag-wrapper {
  display:flex;
  flex-direction:column-reverse;
  margin-top:12px;

  .col-top {
    color:#757575;
    line-height:1;
    padding-bottom:8px;
    margin-bottom:6px;

    .tag-wrap {
      line-height:1;
      margin-top:0;
    }
  }

  .col-btt {
    color:#757575;
    padding-bottom:10px;
    border-bottom:1px solid #D5D5D5;
    margin-bottom:13px;
  }
}

//sec-tab
.sec-tab {
  border:1px solid #D5D5D5;
  border-radius:8px;
  min-height:100%;
  overflow: hidden;
  padding:8px 16px;

  .tabs {
    margin-top:0;

    .form-wrap {
      &.radio {
        display:flex;
        flex-wrap:wrap;

        .chk-label {
          margin:0;
          width:33.33%;
        }
      }
    }
  }
}

.mark-container {
  text-align: left;

  .mark-list {
    margin-top:15px;

    & > li {
      width:100%;
      border-bottom:1px solid #E2E2E2;

      .flex-tag {
        margin:0 -3px;
        color:#333;
        padding:18px 0;
        display:flex;
        align-items: center;

        .col {
          text-align: right;
          padding:0 3px;
          flex-basis:auto;
        }

        .col-mark {
          text-align: left;
          width:calc(100% - 130px)
        }

        .col-tag {
          width:45px;
        }

        .col-time {
          width:50px;
          color:#757575;
        }

        .col-btn {
          width:35px;
          text-align: center;

          .link-item {
            color:#333;
          }

          .btn-ico {
            display:inline-block;
          }
        }

        .mark {
          position:relative;
          padding-left:35px;
          line-height:20px;
          background-color:transparent;

          &::before {
            content:"";
            display:block;
            width:20px;
            height:20px;
            background-size:cover;
            background-position: center;
            position:absolute;
            left:0;
            top:50%;
            transform: translate(0, -50%);
          }

          &.red {
            &::before {
              background-image:url(../images/icon/symbol_red.svg)
            }
          }

          &.blue {
            &::before {
              background-image:url(../images/icon/symbol_blue.svg)
            }
          }

          &.purple {
            &::before {
              background-image:url(../images/icon/symbol_purple.svg)
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .vod-layout {
    margin:0 -5px;

    .vod-left,
    .vod-tab {
      padding:0 5px;
    }
  }
}

@media screen and (max-width: 860px) {
  .page-landing {
    & > .m-main {
      padding:0;
    }
  }

  .vod-layout {
    padding-top:0;

    .vod-left,
    .vod-tab {
      width:100%;
    }

    .vod-left {
      //margin-bottom:15px;

      .box-wrap {
        padding-top:0;
        border-radius: 0;

        .vod-outer {
          margin:0 -16px;
          border-radius: 0;
        }

        .vod-container {
          border-radius: 0;
        }
      }
    }

    .vod-tab,
    .sec-tab,
    .tab-container,
    .tabs,
    .tab-content,
    .tab-item,
    .content-item {
      height:auto !important;
    }

    .vod-tab {
      .sec-tab {
        border:0;
        border-radius: 0;

        .tab-content {
          .tab-item {
            &.clip,
            &.tag {
              .content-item {
                padding-bottom:45px;
              }
            }
          }
        }
      }
    }

    .tab-item {
      .card-flex {
        & > li {
          width:50%;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .vod-layout {
    .tab-item {
      .card-flex {
        & > li {
          width:100%;
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .appHeader + #contents {
    .page-landing {
      margin-top:-36px;
    }
  }

  .page-landing {
    padding-top:64.42%;
    padding-bottom:0 !important;

    .sec-vod {
      width:100%;
      position:fixed !important;
      top:0;
      left:0;
      z-index:200;

      .vod-container {
        padding-top:64.42%;
        height:0;
        overflow: hidden;
        border-radius: 0;

        .video {
          width:100%;
          max-height:100%;
          position:absolute;
          left:50%;
          top:50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    .sec-tab {
      .tab-container {
        padding-top:10px;

        .tabs {
          position:relative;
          margin-top:0;
          z-index:100;
        }
      }

      .sticky {
        z-index:10;
      }

      .tab-nav {
        position:absolute;
        top:0;
        width:100%;
        background:#fff;

        &._sticky {
          position:fixed;
          top:0;
          z-index:300;
        }
      }
    }
  }

  .vod-layout {
    padding-top:0;

    .vod-left,
    .vod-tab {
      width:100%;
    }

    .vod-left {
      margin-bottom:0;

      .box-wrap {
        background: transparent;
        border-radius: 0;
        padding: 0;

        .vod-outer {
          margin:0;
        }
      }
    }

    .vod-tab {
      .btt-btn {
        &.t1 {
          margin-top:0;
          padding:10px 16px;
          background:#fff;
          position:fixed;
          bottom:0;
          left:0;
          right:0;
          z-index:100;
        }
      }
    }
  }

  .sec-tab {
    border:0;
    border-radius:0;
  }

  .sec-info {
    border:0;
    border-radius:0;
    margin-top:0;

    & > {
      .tag-wrapper {
        display:none;
      }
    }
  }

  .info-container {
    & > .m-main {
      padding:0 15px;
    }

    .tit-header {
      border-bottom:1px solid #E2E2E2;
      padding-bottom:8px;

      .top-flex {
        border-bottom:0;
        padding-bottom:0;
      }

      .col-right {
        .dec {
          display:none;
        }
      }
    }

    .tag-container {
      display:block;
      margin-top:8px;
      border-radius:8px;
    }
  }

  .tag-container {
    .tag-wrapper {
      padding:0;
      background:transparent;
      border-radius:0;
      border:0;
      flex-direction: column;

      .col-top {
        border-bottom:1px solid rgba(233, 233, 233, 1);
      }

      .col-btt {
        border-bottom:none;
        margin-bottom:0;
        padding-bottom:0;
      }
    }
  }

  .card-container {
    margin-top:11px;
    padding:16px;
  }
}

@media screen and (max-width: 320px) {
  .page-landing {
    padding-top:205px;

    .sec-vod {
      .vod-container {
        padding-top:205px;
      }
    }
  }
}





