.pop-layer {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 900;

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
  }

  .pop-container {
    position: absolute;
    left: 18px;
    right: 18px;
    top: 50%;
    transform: translate(0, -50%);
  }

  .pop-content {
    width: 100%;
    max-width: 300px;
    margin: 5px auto;
    background: #fff;
    border-radius: 8px;
    overflow: hidden;

    &.confirm-container,
    &.alert-container {
      width: 100%;
      text-align: center;
      padding: 24px;
      padding-top: 30px;
      max-width: 300px;

      .box {
        width: 100%;
        margin: 0 auto;
        margin-top: 20px;
        background: #f7f7f7;
        border-radius: 8px;
        padding: 14px 18px;
        font-size: 13px;

        dl {
          display: flex;
          justify-content: space-between;

          dt {
            padding-right: 10px;
            position: relative;
            font-weight: 600;

            &::after {
              content: ":";
              position: absolute;
              right: 0;
              top: 0;
            }
          }
        }
      }

      .btn-ul {
        margin-top: 23px;
        justify-content: center;

        & > li {
          width: 100%;
        }

        &.f2,
        &.col-50 {
          & > li {
            width: 50%;
          }
        }
      }
    }

    .dec-wrapper {
      white-space: pre-wrap;

      &.nowrap {
        white-space: nowrap;
      }

      strong {
        display: block;
        font-size: 16px;
        margin: 10px 0;

        span {
          display:block;
        }

        small {
          display: block;
          font-size: 12px;
          line-height: 1.2;
          font-weight: 600;
        }
      }

      .dec {
        margin-top: 5px;
        line-height: 1.3;
        font-size: 13px;
      }
    }

    &.login {
      padding: 0;
      padding-top: 38px;

      .dec-wrapper {
        padding-top: 108px;
        font-size: 14px;

        &::before {
          width: 93px;
          height: 93px;
          background-size: 42px auto;
        }
      }

      .btn-ul {
        margin-top: 40px;

        .btn {
          padding: 10px;
          border-radius: 0;
        }
      }
    }

    &.alert-charge {
      strong {
        line-height:1.2;

        span + small {
          margin-top:10px;
        }
      }
    }
  }

  .pop-header {
    position: relative;
    text-align: center;
    border-bottom: 1px solid #dad7d7;
    padding: 17px 10px;

    .tit {
      font-size: 17px;
      font-weight: 700;
      line-height: 1;
    }

    a {
      position: absolute;
      right: 25px;
      color: #333;
    }
  }
}

//새로운 메시지
.msg-container {
  border-radius: 24px;

  .top-flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 20px;
    position: relative;
    border-bottom: 1px solid #dad7d7;

    .col-left {
      display: flex;
      width: calc(100% - 24px);
      padding-right: 10px;

      dt {
        color: #797979;
        width: 60px;
      }

      dd {
        width: calc(100% - 60px);

        input {
          width: 100%;
          border: 0;
        }
      }
    }
  }

  .msg-content {
    padding: 18px 22px;

    .box {
      padding: 16px 15px;
      border: 1px solid #e9e9e9;
      border-radius: 12px;
      box-shadow: 0px 2px 2px 0px #0000000d;

      .img-box {
        width: 100%;
        height: 130px;
        border-radius: 12px;
        background: #f0f5f8;
        position: relative;

        &::before {
          content: "";
          display: block;
          background: #fff url("../images/icon/logo symbol.svg") center
            no-repeat;
          background-size: 31px 21px;
          width: 51px;
          height: 51px;
          border-radius: 100%;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
        }
      }

      .dec-wrap {
        margin-top: 20px;
      }
    }

    textarea {
      width: 100%;
      margin-top: 20px;
      height: 100px;
      border: 0;
      resize: none;
    }
  }
}

