#footer {
  border-top: 1px solid #e5e5e5;
  padding: 20px 0;
  width: 100%;
  max-width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  // margin-top: auto;

  .footer_wrap {
    ul {
      display: flex;
      align-items: center;
      font-size: 0.8125rem;
      list-style-type: none;
      padding: 0;
      margin: 0;

      a {
        text-decoration: none;
        color: #404048;
        padding-right: 30px;
      }
    }

    .board_list li {
      position: relative;

      &:not(:last-child)::after {
        content: "";
        width: 1px;
        height: 19px;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        background-color: #e0e0e0;
        margin-right: 15px;
      }
    }

    .corporate {
      margin-top: 12px;
      font-size: 11px;
      font-weight: 500;
      line-height: 1;
      color: #757575;
    }
  }
}
