#header {
  .navbar-header {
    /*height: 69px;
    align-items: end;*/

    .nav {
      width: 50%;
      font-size: 1rem;
      font-weight: bold;
      color: #999999;
      gap: 4.5px;

      li {
        width: 71px;
        height: 40px;
        text-align: center;
        line-height: 40px;
      }

      /*.active {
        border-bottom: 3px solid #3af374;

        a {
          color: #002365;
        }
      }

      a {
        text-decoration: none;
        color: #999999;
      }*/
    }
  }

  .nav {
    .item-wrapper {
      text-align: center;

      &.active {
        .nav-item {
          color: #002365;

          &::before {
            content: "";
            display: block;
            height: 3px;
            width: 100%;
            background: #3af374;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
          }
        }
      }

      .nav-item {
        text-decoration: none;
        color: #999999;
        position: relative;
        display: inline-block;
        padding: 23px 0;
        white-space: nowrap;
      }
    }
  }

  .navbar-brand {
    .logo {
      display: flex;
      align-items: center;

      img {
        max-width: 112px;
        max-height: 25.5px;
      }
    }
  }

  // 헤더 아이콘 및 로그인
  .wrap_service {
    display: flex;
    /*margin-bottom: 5px;*/
    align-items: center;

    & > div + div {
      margin-left: 16px;
    }
  }

  .wrap_search {
    position: relative;
    // overflow: hidden;
    display: flex;
    align-items: center;
    &.open {
      .lately_search {
        width: 100%;
        position: absolute;
        top: 100%;
        left: 0;
        background-color: white;
        border: 1px solid #ccc;
        box-shadow: 0 2px 8px rgba(236, 232, 232, 0.15);
        padding: 10px;
        display: block;
        z-index: 10;
        max-height: 300px;
        overflow-y: auto;

        .lately-container {
          dt {
            font-size: 0.75rem;
            padding: 0 11px 10px 11px;
            flex-wrap: wrap;
          }
          .lately-tag {
            display: flex;
            flex-direction: column;
            max-height: 100%;
            li {
              margin: 0;
              span {
                width: 100%;
                background-color: #fff;
                border: none;
                padding: 0;
                display: block;

                button {
                  margin: 0;
                }
              }
            }
          }
        }
      }
    }
  }

  .wrap_search.open {
    .wrap_search {
      position: relative;
      overflow: hidden;
    }
  }

  .wrap_search .search-form {
    display: none;
    padding: 0;
    width: 220px;

    .form-wrap {
      &._round {
        border-radius: 4px;
      }
    }
  }

  .wrap_search.open .search-form {
    display: block;
  }

  .btn_search,
  .link_ticket,
  .btn_alarm,
  .btn_mypage {
    display: inline-flex;
    align-items: center;
    height: 20px;
    padding: 0 16px;
  }

  .btn_search {
    background-image: url("../images/icon/Search.svg");
    //   vertical-align: middle;
  }

  .link_ticket {
    background-image: url("../images/icon/ticket.svg");
  }

  .btn_alarm {
    //   position: relative;
    background-image: url("../images/icon/Bell.svg");
  }

  .header .btn_mypage,
  .btn_login,
  .btn_user {
    width: auto;
    color: #333333;
    font-size: 0.8125rem;
    text-decoration: none;
    white-space: nowrap;
  }

  .btn_login,
  .btn_user {
    a {
      color: #333333;
      text-decoration: none;
      white-space: nowrap;
    }

    span {
      color: #333333;
    }
  }

  .icon_20 {
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-position: center;
  }

  .icon_26 {
    width: 26px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center;
  }

  // 드롭다운

  .wrap_login {
    position: relative;
  }

  .dropdown-menu {
    min-width: 280px;
    position: absolute;
    top: 100%;
    left: -230px;
    background-color: white;
    border: 1px solid #ccc;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    z-index: 2000;
    padding: 10px;
    display: block;
    .my-info {
      .info-top {
        gap: 10px;
        img {
          width: 50px;
          border-radius: 100%;
        }
        .name {
          font-weight: 700;
          font-size: 0.875rem;
          display: block;

          a {
            display: block;
            padding: 0;
            font-size: 0.625rem;
            font-weight: 400;
            padding-right: 10px;
            text-decoration: none;
            color: #666666;
          }
        }
      }

      .dec {
        color: #757575;
      }

      .ball-box {
        background: #f0f5f8;
        border: 1px solid #e9e9e9;
        border-radius: 6px;
        margin-top: 18px;
        padding: 20px 19px 12px;

        .ball-flex {
          justify-content: space-between;
          padding-bottom: 10px;
          border-bottom: 1px solid #e9e9e9;
          margin-bottom: 9px;
        }

        .ball {
          position: relative;
          display: inline-flex;
          align-items: center;
          padding-left: 27px;
          font-size: 16px;
          font-weight: 700;

          small {
            font-weight: 400;
            font-size: 13px;
          }
        }

        .btn-charge {
          padding: 3px 10px;
          line-height: 1;
          border-radius: 22px;

          .txt {
            padding-left: 16px;
          }
        }

        .btn-season {
          font-weight: 500;
          border-radius: 24px;
        }

        .btt-flex {
          justify-content: space-between;
          font-size: 14px;
          font-weight: 700;
          color: rgba(51, 51, 51, 1);

          .btn-season {
            .ico.season {
              padding-left: 25px;

              &::before {
                width: 19px;
                height: 11px;
                background-image: url("../images/icon/ticket_blue.svg");
              }
            }
          }
        }
      }
    }
    .info_menu {
      display: flex;
      flex-direction: column;
      padding: 15px;
      gap: 10px;

      a {
        font-size: 0.875rem;
        font-weight: 400;
        color: #000;
        text-decoration: none;
      }
    }
  }

  // 반응형 스타일
  @media (max-width: 360px) {
    #header {
      width: 360px;

      .navbar-header {
        height: 36px;
        align-items: center;
        margin: 8px 10px 0 10px;

        .logo {
          display: flex;
          align-items: center;
          padding: 0;
          img {
            width: 83.5px;
            height: 19px;
          }
        }
      }

      .nav {
        display: flex;
        justify-content: center;
        font-size: 0.9375rem;
        font-weight: bold;
        color: #999999;
        // margin: 0 10px;
        gap: 6px;

        li {
          width: 66px;
          height: 42px;
          text-align: center;
          line-height: 32px;
        }

        .active {
          border-bottom: 3px solid #3af374;

          a {
            color: #002365;
          }
        }

        a {
          text-decoration: none;
          color: #999999;
        }
      }

      // 헤더 아이콘 및 로그인
      .wrap_service > div + div {
        margin-left: 10px;
      }
    }
  }
  /* Frame 481306 */
}

@media (max-width: 480px) {
  #header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background-color: #fff;

    .navbar-header {
      height: 36px;
      align-items: center;
      margin: 8px 10px 0 10px;

      .logo {
        display: flex;
        align-items: center;
        padding: 0;

        img {
          width: 83.5px;
          height: 19px;
        }
      }
    }

    .nav {
      padding: 0 16px;
      display: flex;
      justify-content: center;
      font-size: 0.9375rem;
      font-weight: bold;
      color: #999999;
      gap: 6px;

      .slick-slide {
        max-width: 70px !important;
      }

      li {
        width: 66px;
        height: 42px;
        text-align: center;
        line-height: 42px;
      }

      .item-wrapper {
        .nav-item {
          padding: 13px 0;
        }
      }

      /*.active {
        border-bottom: 3px solid #3af374;
        color: #002365;

        a {
          color: #002365;
        }
      }

      a {
        text-decoration: none;
        color: #999999;
      }*/
    }

    .wrap_service > div + div {
      margin: 0px;
    }
  }
}

@media (min-width: 581px) and (max-width: 700px) {
  .wrap_service {
    // min-width: 135px;
  }
  #header .wrap_service > div + div {
    margin-left: 0px;
  }
  .wrap_search .search-form {
    max-width: 180px;
  }
}

@media (min-width: 481px) and (max-width: 580px) {
  .wrap_search .search-form {
    max-width: 150px;
  }
}

.mobile_nav {
  width: 100%;

  .mobile_nav_wrapper {
    position: fixed;
    width: 100%;
    height: 77px;
    background-color: #fff;
    padding: 0 10px;
    bottom: 0;
    display: flex;
    z-index: 40;
    align-items: center;
    justify-content: space-between;
  }

  .nav_link {
    text-decoration: none;
    color: #333333;
    font-size: 0.8125rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  p {
    margin: 0;
    padding-top: 5px;
  }

  .active {
    p {
      color: #13009f;
    }

    img {
      width: 30px;
      height: 30px;
    }
  }

  .nav_link_img {
    width: 30px;
    height: 30px;

    img {
      width: 20px;
      height: 20px;
    }
  }
}

.appHeader {
  height: 36px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  color: #333333;
  background-color: transparent;
  font-size: 1rem;
  font-weight: 700;
  z-index: 100;
  transition: background-color 0.3s ease;
  overflow: hidden;
}

.appHeader.content {
  top: 36;
  background-color: transparent !important;
}

.appHeader.scrolled {
  background-color: #fff !important;
}
.appHeader .logo {
  width: 20%;
}

.appHeader .wrap_service {
  width: 20%;
}

.appHeader .wrap_service {
  display: flex;
  gap: 5px;
  align-items: center;
  justify-content: flex-end;
}

.appHeader .wrap_service.scrolled {
  gap: 8px;
  margin: 0;
}

.appHeader img {
  transition: opacity 0.3s ease;
}

.icon-small {
  width: 20px;
  height: 20px;
}

.icon-large {
  width: 25px;
  height: 25px;
}
