// btn
a {
  text-decoration: none;
}

button {
  border: 0;
  outline: 0;
  background-color: transparent;
}

.btn:active,
.btn:focus {
  outline: none !important;
  box-shadow: none !important;
}

.btn {
  background-color: #2f52ff;
  color: #fff;
  text-align: center;
  font-size: 0.875rem;
  font-weight: 700;
  border-radius: 8px;
  white-space: nowrap;
  position: relative;

  &._non_radius {
    border-radius: 0;
  }

  // size
  &.btn-xxs {
    padding: 5px 10px;
    line-height: 1;
    font-size: 12px;
    font-weight: 400;
  }

  &.btn-xs {
    padding: 7px 10px;
    line-height: 1;
    font-size: 12px;
    font-weight: 400;
  }

  &.btn-small {
    padding: 9px 10px;
  }

  &.btn-lg {
    padding-top: 12px;
    padding-bottom: 13px;
    line-height: 1;
    font-size: 13px;
    font-weight: 700;
  }

  &.btn-xl {
    padding-top: 14px;
    padding-bottom: 15px;
    line-height: 1;
    font-size: 13px;
    font-weight: 700;
  }

  &.btn-xxl {
    padding-top: 16px;
    padding-bottom: 16px;
    line-height: 1;
    font-size: 14px;
    font-weight: 700;
  }

  // color
  &.btn-line-white {
    border: 1px solid #e9e9e9;
    background-color: #fff;
    color: #333;

    .ico {
      color: #666;
    }
  }

  &.btn-line-black {
    border: 1px solid #333;
    background-color: #fff;
    color: #333;

    .ico {
      color: #666;
    }
  }

  &.btn-gray {
    color: #fff;
    background-color: #d2d2d2;
  }

  &.btn-black {
    color: #fff;
    background-color: #000;
  }

  &.btn-red {
    color: #fff;
    background-color: #ff3b30;
  }

  &.btn-sky {
    color: #333;
    background-color: #eff3f8;
    border-color: #eff3f8;
  }

  &.btn-light-gray {
    color: rgba(102, 102, 102, 1);
    background-color: rgba(245, 245, 245, 1);
    border-color: rgba(245, 245, 245, 1);
  }

  &.btn-light-gray-stroke {
    color: #333;
    background-color: #f7f7f7;
    border-color: #e9e9e9;
  }

  &.btn-light-blue-stroke {
    color: #304ffe;
    background-color: #fff;
    border-color: #304ffe;
  }

  &.btn-dark-gray-stroke {
    color: #fff;
    background-color: #757575;
    border-color: #e9e9e9;
  }

  &.white {
    background-color: #ffffff;
  }

  &.btn-line-white-nb {
    border: none;
    background-color: #fff;
    color: #333;
  }

  &:disabled {
    border-color: #d2d2d2;
    background: #d2d2d2;
    color: #fff;
  }

  &.round {
    border-radius: 28px;
  }

  .ico {
    line-height: 1;
    display: inline-block;
    position: relative;
    padding-left: 15px;

    &::before {
      content: "";
      display: block;
      width: 13px;
      height: 13px;
      background-size: cover;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
    }

    &.clip {
      &::before {
        background-image: url("../images/icon/icon_clip.svg");
      }
    }

    &.tag {
      &::before {
        background-image: url("../images/icon/icon_tag.svg");
      }
    }
  }

  &.list-btn {
    width: 60px;
    height: 22px;
    border-radius: 100px;
  }

  &.img-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-weight: 500;
  }

  &.btn-ico,
  &.width-txt {
    display: block;
    background-color: transparent;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 0;
    border: 0;
    border-radius: 0;
  }

  .btn-ico {
    .txt {
      position: absolute !important;
      left: -10000px !important;
      top: -10000px !important;
      width: 0;
      height: 0;
      font-size: 0;
      display: block;
      visibility: hidden;
    }
  }

  &.width-txt {
    width: auto !important;
    height: auto !important;
    white-space: nowrap;
    color: #fff;
    text-align: left;
    background-position: left center;

    &.ico-clip {
      background-size: 17px;
      padding-top: 20px;
    }

    &.ico-tag {
      background-size: 15px;
      padding-top: 18px;
    }

    &.ico-save {
      background-size: 14px 17px;
      padding-top: 20px;
    }

    &.ico-cancel {
      background-size: 13px;
      padding-top: 16px;
    }

    &.tag {
      padding-left: 28px;
      position: relative;

      &::before {
        content: "";
        display: block;
        width: 22px;
        height: 22px;
        background-color: #fff;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 100%;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(0, -50%);
      }

      &.t1::before {
        background-image: url("../images/icon/tag_1.svg");
      }

      &.t2::before {
        background-image: url("../images/icon/tag_2.svg");
      }

      &.t3::before {
        background-image: url("../images/icon/tag_3.svg");
      }

      &.t4::before {
        background-image: url("../images/icon/tag_4.svg");
      }

      &.t5::before {
        background-image: url("../images/icon/tag_5.svg");
      }
    }

    &.btn-my {
      display: inline-block;
      padding: 0;
      padding-top: 23px;
      text-align: center;
      color: rgba(51, 51, 51, 1);
    }
  }

  .btn-tag {
  }

  &.selectLayer {
    background: #fff;
    padding: 9px 12px;
    font-weight: 400;
    color: rgba(73, 80, 87, 1);
    border-radius: 0.25rem;
    width: 100%;
    text-align: left;

    &::after {
      content: "";
      display: block;
      background: url(../images/icon/icon_arrow_down_black.svg) center no-repeat;
      background-size: cover;
      width: 9px;
      height: 6px;
      position: absolute;
      right: 12px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &.link-item {
    padding: 0;
    background: transparent;
    color: rgba(117, 117, 117, 1);
    font-weight: 400;
    border: 0;
    font-size: 11px;
  }
}

.link-item {
  text-decoration: none !important;
}

.ico {
  position: relative;

  &::before {
    content: "";
    display: block;
    background-size: cover;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
  }

  &.ico-back {
    width: 14px;
    height: 24px;
    background-image: url("../images/icon/icon_arrow_left_white.svg");

    &.black {
      background-image: url("../images/icon/icon_arrow_left.svg");
    }
  }

  &.ico-alarm,
  &.ico-share,
  &.ico-heart {
    width: 17px;
    height: 17px;
  }

  &.ico-alarm {
    background-image: url("../images/icon/icon_claim.svg");
  }

  &.ico-share {
    background-image: url("../images/icon/icon_share_gray.svg");
  }

  &.ico-heart {
    background-image: url("../images/icon/icon_heart.svg");

    &._active {
      background-image: url("../images/icon/Icon_heartstroke.svg");
    }
  }

  &.ico-tag {
    background-image: url("../images/icon/icon_tag_white.svg");
    width: 18px;
    height: 18px;
  }

  &.ico-screencast {
    background-image: url("../images/icon/icon_screencast.svg");
    width: 17px;
    height: 15px;
  }

  &.ico-multiview {
    background-image: url("../images/icon/icon_multiview.svg");
    width: 20px;
    height: 15px;
  }

  &.ico-vol {
    background-image: url("../images/icon/icon_volume.svg");
    width: 21px;
    height: 21px;
  }

  &.ico-speed {
    background: url("../images/icon/icon_speed.svg") center no-repeat;
    width: 21px;
    height: 21px;
    background-size:20px;
  }

  &.ico-picture {
    background: url("../images/icon/icon_picture.svg") center no-repeat;
    width: 21px;
    height: 21px;
    background-size:17px;
  }

  &.ico-full {
    background-image: url("../images/icon/icon_zoom.svg");
    width: 17px;
    height: 18px;
  }

  &.ico-tag {
    background-image: url("../images/icon/icon_tag_white.svg");
    width: 14px;
    height: 14px;
  }

  &.ico-clip {
    background-image: url("../images/icon/icon_clip_white.svg");
    width: 17px;
    height: 17px;
  }

  &.ico-down {
    padding-left: 0;
    padding-right: 16px;

    &::before {
      background-image: url("../images/icon/icon_arrow_down.svg");
      width: 12px;
      height: 7px;
      left: auto;
      right: 0;
    }
  }

  &.ico-etc {
    background-image: url("../images/icon/icon_etc.svg");
    width: 3px;
    height: 14px;
  }

  &.ico-save {
    background-image: url("../images/icon/icon_save.svg");
    width: 14px;
    height: 17px;
  }

  &.ico-cancel {
    background-image: url("../images/icon/icon_cancel.svg");
    width: 13px;
    height: 13px;
  }

  &.ico-gray-cancel {
    background-image: url("../images/icon/ico_inputdels.svg");
    width: 13px;
    height: 13px;
  }

  &.icon-arrow-down {
    background-image: url("../images/icon/icon_arrow_down_black.svg");
    width: 9px;
    height: 6px;
    transition-duration: 0.6s;

    &::before {
      display: none;
    }

    &._open {
      transform: rotate(180deg);
    }
  }

  &.vod-play,
  &.vod-pause {
    width: 50px;
    height: 50px;
  }

  &.vod-play {
    background-image: url("../images/icon/vod_play.svg");
  }

  &.vod-pause {
    background-image: url("../images/icon/vod_pause.svg");
  }

  &.ico-prev,
  &.ico-next {
    width: 6px;
    height: 11px;
    background-image: url("../images/icon/icon_prev.svg");
  }

  &.btn-prev,
  &.btn-next {
    background-image: url("../images/icon/icon_next.svg");
    width: 14px;
    height: 24px;
  }

  &.btn-gray-next {
    background-image: url("../images/icon/icon_arrow_right_gray.svg");
    width: 5px;
    height: 12px;
  }

  &.btn-prev {
    transform: rotate(180deg);
  }

  &.ico-next {
    transform: rotate(180deg);
  }

  &.ico-add {
    background-image: url("../images/icon/icon_add_circle.svg");
    width: 24px;
    height: 24px;
  }

  &.ico-close {
    background-image: url("../images/icon/icon_close.svg");
    width: 24px;
    height: 24px;
  }

  &.ico-close-white {
    background-image: url("../images/icon/icon_close_white.svg");
    width: 12px;
    height: 12px;
  }

  &.ico-close-black {
    background-image: url("../images/icon/icon_close_black.svg");
    width: 27px;
    height: 27px;
  }

  &.link-del {
    position: relative;
    padding-right: 14px;
    color: #5c5c5c;

    &::before {
      content: "";
      display: block;
      background: url("../images/icon/ico_del_gray.svg") center no-repeat;
      width: 12px;
      height: 12px;
      position: absolute;
      left: auto;
      right: 0;
      top: 50%;
      transform: translate(0, -50%);
    }
  }

  &.ico-copy {
    background-image: url("../images/icon/btn_addresscopy.svg");
    width: 56px;
    height: 16px;
  }

  &.ico-dn {
    background-image: url("../images/icon/chkstate=dn.svg");
    width: 15px;
    height: 10px;
  }

  &.ico-up {
    background-image: url("../images/icon/chkstate=up.svg");
    width: 15px;
    height: 10px;
  }

  &.ico-edit {
    background-image: url("../images/icon/ic_edit.svg");
    width: 42px;
    height: 42px;
  }

  &.ico-edit2 {
    background:transparent url("../images/icon/icon_etc_white.svg") center top 2px no-repeat;
    background-size:auto 24px;
    width: 18px;
    height: 18px;
  }

  &.ico-download {
    background:#fff url("../images/icon/icon_down.svg") center no-repeat;
    background-size:auto 12px;
    border-radius: 100%;
    width: 18px;
    height: 18px;
  }

  &.ico-gray-edit {
    background-image: url("../images/icon/ico_light_gray_edit.svg");
    width: 20px;
    height: 20px;
  }

  &.ico-del {
    background-image: url("../images/icon/ico_del_gray.svg");
    width: 12px;
    height: 12px;
  }

  &.ico-blackdel {
    background-image: url("../images/icon/ico_blackdel.svg");
    width: 18px;
    height: 18px;
  }

  &.ico-qr {
    padding-left: 24px;

    &::before {
      background-image: url("../images/icon/icon_qr.svg");
      width: 18px;
      height: 18px;
    }
  }

  &.ball {
    &::before {
      background-image: url("../images/icon/icon_ball_blue.svg");
      width: 17px;
      height: 17px;
    }
  }

  &.charge {
    &::before {
      background-image: url("../images/icon/ico_add_circle.svg");
      width: 13px;
      height: 12px;
    }
  }

  &.more {
    padding-right: 10px;

    &:after {
      content: "";
      display: block;
      background-image: url("../images/icon/icon_arrow_left.svg");
      background-size: cover;
      width: 7px;
      height: 12px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(0, -50%) rotate(180deg);
    }
  }

  &.season {
    padding-left: 24px;

    &:before {
      background-image: url("../images/icon/ticket.svg");
      width: 21px;
      height: 13px;
    }
  }

  &.info {
    padding-left: 18px;
    margin: 0 auto;
    color: rgba(153, 153, 153, 1);
    display: inline-block;

    &::before {
      width: 14px;
      height: 14px;
      background-image: url("../images/icon/icon-info.svg");
    }
  }

  &.refesh {
    padding-left: 13px;

    &::before {
      width: 11px;
      height: 13px;
      background-image: url("../images/icon/icon_refresh.svg");
    }
  }

  &.btn-my {
    &::before {
      left: 50%;
      top: 0;
      transform: translate(-50%, 0);
    }

    &.info {
      &::before {
        width: 15px;
        height: 17px;
        background-image: url("../images/icon/icon_my_ogb1.svg");
      }
    }

    &.custom {
      &::before {
        width: 19px;
        height: 19px;
        background-image: url("../images/icon/icon_my_ogb2.svg");
      }
    }

    &.history {
      &::before {
        width: 18px;
        height: 15px;
        background-image: url("../images/icon/icon_my_ogb3.svg");
      }
    }
  }

  &.kakao {
    border-radius: 4px;
    background: rgba(251, 230, 0, 1) url("../images/icon/icon_kakao.svg") center
      no-repeat;
    background-size: 30px auto;
    width: 40px;
    height: 40px;
  }

  &.btn-remove2 {
    width: 18px;
    height: 18px;
    border-radius: 100%;
    background: #000 url(../images/icon/icon_ex.svg) center no-repeat;
    background-size: 8px;
  }
}

.btn-ul {
  margin: 0 -2px;

  & > li {
    padding: 0 2px;
    margin-bottom: 5px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &.f2 {
    margin: 0 -5px;

    & > li {
      margin: 0;
      width: 50%;
      padding: 0 5px;

      .btn {
        width: 100%;
      }
    }
  }
}

.more {
  color: #666;
  position: relative;
  padding-right: 7px;

  &::after {
    content: "";
    display: block;
    width: 5px !important;
    height: 12px !important;
    background-image: url("../images/icon/icon_arrow_right_gray.svg");
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
  }

  .c-black {
    padding-right: 13px;
    color: rgba(51, 51, 51, 1);

    &:after {
      background-image: url("../images/icon/icon_arrow_right_black.svg");
      width: 6px;
      height: 9px;
    }
  }
}

.btn-tag {
  padding: 0;
  padding-right: 36px;
  display: inline-block;
  line-height: 1;
  border-radius: 35px;
  overflow: hidden;
  position: relative;
  background-color: #eff3f8;
  border-color: #eff3f8;

  .btn-xs {
    padding: 9px 10px;
    color: #333;
    background-color: transparent;
    white-space: nowrap;
  }

  .ico-del {
    width: 36px;
    height: 32px;
    position: absolute;
    right: 0;
    top: 0;
    background-position: center;
    background-size: 12px;
  }

  .btn {
    padding-right: 0;
  }
}

.btn-add {
  width: 150px;
  height: 40px;
  border: 0.5px solid #000;
  border-radius: 17px;
  background-color: #fff;
  color: #373737;
}

.btn-add:hover,
.btn-add:focus {
  background-color: #373737;
  color: #fff;
}

.btn_arrow {
  width: 12px;
  height: 20px;
}

.btn_out {
  width: 10px;
  height: 10px;
  margin-left: 5px;
}

.tag-item {
  display: inline-block;
  border: 1px solid rgba(233, 233, 233, 1);
  background: #fff;
  color: rgba(117, 117, 117, 1);
  font-weight: 600;
  padding: 6px 10px;
  border-radius: 28px;
}

@media screen and (max-width: 480px) {
  .tag-item {
    border: 0;
    background: transparent;
    padding: 0;
    border-radius: 0;
  }
}

//logo-symbol
.symbol {
  position: relative;
  padding-top: 60px;

  &::before {
    content: "";
    display: block;
    background: rgba(247, 247, 247, 1) url("../images/icon/logo symbol.svg")
      center no-repeat;
    background-size: 31px 21px;
    width: 47px;
    height: 47px;
    border-radius: 100%;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);
  }
}

.fix_btn_wrapper {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  .fix_btn {
    width: 360px;
    position: fixed;
    bottom: 150px;

    &.mobile {
      bottom: 0;
      width: 100%;

      .btn-ul {
        padding: 0;
        li {
          margin: 0;
          padding: 0;
          width: 50%;
          button {
            border-radius: 0;
          }
        }
      }
    }
    .btn-ul {
      li {
        margin: 0;
        width: 180px;
        border-radius: 0;

        .affiliation {
          width: 100%;
        }
      }
    }
  }
}

.btn-drop {
  color: rgba(102, 102, 102, 1);
  display: inline-block;
  position: relative;
  padding-right: 15px;

  &::after {
    content: "";
    display: block;
    width: 9px;
    height: 6px;
    background-image: url("../images/icon/icon_arrow_down_gray.svg");
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.3s;
  }

  &._open {
    &::after {
      transform: translateY(-50%) rotate(180deg);
    }
  }
}

._arrow {
  position: relative;

  &::after {
    content: "";
    display: block;
    background: url("../images/icon/icon_arrow_down_black.svg") center no-repeat;
    background-size: cover;
    width: 9px;
    height: 6px;
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
  }
}
