.bbs-container {
  word-break: keep-all;
  padding-top:24px;

  .col-status {
    width:56px;
  }

  .col-dec {
    width:calc(100% - 56px);
    padding-left:10px;

    .tit {
      color:rgba(51, 51, 51, 1);
      font-weight:600;
      font-size:14px;
      line-height:1.2;
      padding:5px 0;
      display:block;
      width:100%;
    }

    .date {
      display:block;
      color:rgba(117, 117, 117, 1);
      font-size:12px;
      line-height:1;
      font-weight:400;
    }
  }
}

.bbs-list {
  .nodata {
    background:rgba(247, 247, 247, 1);
    border:1px solid rgba(233, 233, 233, 1);
    border-radius:16px;
    padding:40px 0;
    text-align: center;
    margin-top:35px;

    .dec {
      color:rgba(153, 153, 153, 1);
      display:block;
      position:relative;
      padding-top:57px;

      &::before {
        content:"";
        display:block;
        width:47px;
        height:47px;
        border-radius:100%;
        background:#fff url('../images/icon/logo symbol.svg') center no-repeat;
        background-size:30px auto;
        position:absolute;
        left:50%;
        top:0;
        transform:translateX(-50%);
      }
    }
  }

  & > li {
    width:100%;
    border-bottom:1px solid rgba(233, 233, 233, 1);
    margin-bottom:0;

    .item-flex {
      padding:16px;
      align-items: flex-start;

      &:active,
      &:focus,
      &:hover {
        background:rgba(240, 245, 248, 1);
        text-decoration: none !important;
      }

      .col-dec {
        width:100%;

        .tit {
          font-weight:500;
          font-size:13px;
          padding:0;
        }

        .date {
          font-size:10px;
        }
      }
    }
  }

  &.qna {
    & > li {
      .item-flex {
        padding:16px 0;

        .col-status {
          width:56px;
        }

        .col-dec {
          width:calc(100% - 56px);
          padding-left:10px;

          .tit {
            padding:5px 0;
          }
        }
      }
    }
  }
}

.status-label {
  width:100%;
  max-width:56px;
  white-space: nowrap;
  display:inline-block;
  text-align: center;
  padding:6px 0;
  border:1px solid rgba(51, 51, 51, 1);
  background:#fff;
  color:rgba(51, 51, 51, 1);
  font-weight:700;
  font-size:11px;
  border-radius:25px;
  line-height:1;

  &.black {
    background:rgba(51, 51, 51, 1);
    color:#fff;
  }
}

.bbs-detail {
  .detail-wrap {
    & > dt {
      border-bottom:1px solid rgba(233, 233, 233, 1);
      padding-bottom:12px;
      margin-bottom:12px;
    }
    & > dd {
      border-bottom:1px solid rgba(233, 233, 233, 1);
      padding-bottom:25px;
    }

    .tit {
      display:block;
      width:100%;
      font-weight:700;
      font-size:14px;
      line-height:1.2;
    }

    .date {
      color:rgba(117, 117, 117, 1);
      font-size:10px;
      font-weight:400;
    }
  }

  .editor-wrap {
    color:rgba(51, 51, 51, 1);
    line-height:1.4;
  }

  &.qna {
    .detail-wrap {
      margin-top:9px;
      background: rgba(247, 247, 247, 1);
      border-radius:8px;
      padding:16px;

      & > dt {
        padding-bottom:9px;
        margin-bottom:9px;
      }

      & > dd {
        padding-bottom:0;
        border-bottom:0;
      }

      .tit {
        font-weight:600;
      }

      .date {
        font-size:12px;
      }
    }
  }

  .reply-container {
    margin-top:24px;

    .detail-wrap {
      background:rgba(240, 245, 248, 1);
    }
  }
}

.refund-detail {
  .box {
    text-align: center;
    padding:16px;
    border-radius: 12px;
    background:rgba(247, 247, 247, 1);
    border:1px solid rgba(233, 233, 233, 1);
    font-size:12px;

    & > dt {
      color:rgba(98, 98, 98, 1);
      font-weight:700;
      padding-bottom:6px;
      margin-bottom:6px;
      border-bottom:1px solid rgba(233, 233, 233, 1);
    }

    & > dd {
      .tit {
        font-weight:600;
        display:block;
        width:100%;
        color:rgba(51, 51, 51, 1);
      }

      .dec {
        color:rgba(117, 117, 117, 1);
      }
    }
  }

  .item-flex {
    align-items: flex-start;
  }

  .detail-wrap {
    & > dt {
      padding-bottom:16px;
      margin-bottom:16px;
      border-bottom:1px solid rgba(233, 233, 233, 1);
    }

    & > dd {
      .box {
        border:0;
        text-align: left;
      }
    }
  }

  .info-wrap {
    margin-top:24px;
    margin-bottom:16px;

    & > dt {
      padding-bottom:9px;
      margin-bottom:9px;
      border-bottom:1px solid rgba(233, 233, 233, 1);
      font-weight:700;
      color:rgba(51, 51, 51, 1);
    }

    & > dd {
      & > dl {
        margin-bottom:6px;
        display:flex;
        justify-content: space-between;
        font-weight:600;
        font-size:12px;

        &:last-child {
          margin-bottom:0;
        }

        & > dt {
          color:rgba(117, 117, 117, 1);
        }

        & > dd {
          text-align: right;
          color:rgba(51, 51, 51, 1);
        }
      }
    }
  }

  .term {
    text-align: left;
    font-size:12px;

    dt {
      padding-bottom:10px;
      font-weight:400;
      color:rgba(51, 51, 51, 1);
    }

    dd {
      font-size:11px;
      color:rgba(102, 102, 102, 1);
    }
  }
}