// 카드리스트
.card-flex {
  margin: 0 -6px;

  & > li {
    padding: 6px;
    width: 33.33%;

    &:last-child {
      margin-bottom: 0;
    }

    .card_wrapper {
      width: 100%;

      min-width: 0;
      height: auto;
      margin: 0;
      position: relative;
      overflow: hidden;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;

      &.clip,
      &.video {
        /*height:0;
        padding-top:38.41%;*/
        height: 150px;

        .card_top,
        .card_bottom {
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }

        .card_top {
          .col-btn {
            display:flex;

            & > li {
              padding:0 3px;
            }
          }
        }

        .card_bottom {
          top: auto;
          bottom: 0;
        }
      }

      .card_bottom {
        border-radius: 0;
        height: auto;
        background-size: cover;
        background-position: center;
      }

      &.schedule {
        .card_top {
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }

        .card_bottom {
          height: 150px;
        }
      }
    }

    .nodata-box {
      max-width: 100%;
    }

    &.f1 {
      width: 100%;

      //LiveCard
      .card_wrapper {
        &.live {
          .card_top {
            .live_icon {
              img {
                display: none;
              }
            }
          }

          .game-info {
            display: inline-flex;

            dt {
              font-size: 14px;
              margin-right: 7px;
            }

            dd {
              .stadium {
                display: inline-block;
                font-size: 12px;
                line-height: 1;
                padding: 5px 12px;
                color: rgba(0, 35, 101, 1);
                border: 1px solid rgba(206, 222, 238, 1);
                background: rgba(206, 222, 238, 1);
                border-radius: 24px;
              }
            }
          }

          .stadium,
          .game_date {
            display: none;
          }

          .game_score {
            font-size: 20px;
            padding: 11px 0;
          }

          .card-team {
            display: flex;
            flex-wrap: nowrap;
            width: auto;
            flex-direction: row;
            align-items: center;
            font-size: 13px;
            font-weight: 600;
            color: rgba(0, 35, 101, 1);

            dt {
              img {
                height: 30px;
              }
            }

            dd {
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 950px) {
  .card-flex {
    & > li {
      &.f1 {
        //LiveCard
        .card_wrapper {
          &.live {
            .card_top {
              .live_icon {
                img {
                  display: block;
                }
              }
            }

            .game-info {
              display: none;
            }

            .stadium,
            .game_date {
              display: block;
            }

            .game_score {
              font-size: 0.75rem;
              padding: 5px 0;
            }

            .card-team {
              width: 100px;
              flex-direction: column;
              align-items: center;
              grid-gap: 10px;
              gap: 10px;
              font-size: 0.625rem;

              dd {
                padding: 0;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 880px) {
  .card-flex {
    & > li {
      width: 50%;
    }
  }
}

@media screen and (max-width: 768px) {
  .card-flex.t2 {
    & > li {
      width: 50%;
    }
  }
}

@media screen and (max-width: 600px) {
  .card-flex {
    & > li {
      width: 100%;
    }
  }
}

@media screen and (max-width: 480px) {
  .card-flex.t2 {
    & > li {
      width: 100%;
    }
  }
}

.card_wrapper {
  &.society {
    width: 100%;
    height: 150px !important;
    position: relative;
    background: rgba(241, 241, 241, 1);
    border-radius: 12px;
    overflow: hidden;
    display: flex;
    align-items: center;
    padding: 0 24px;
    gap: 0;

    &::before,
    &::after {
      content: "";
      width: 108px;
      display: block;
      height: 100%;
      position: absolute;
      top: 0;
      z-index: 0;
      background-size: auto 100%;
    }

    &::before {
      left: 0;
      background-position: left top;
    }

    &::after {
      right: 0;
      background-position: right bottom;
    }

    &.bg_1 {
      &::before,
      &::after {
        background-image: url(../images/card/society_card1.svg);
      }
    }

    &.bg_2 {
      &::before,
      &::after {
        background-image: url(../images/card/society_card2.svg);
      }
    }

    &.bg_3 {
      &::before,
      &::after {
        background-image: url(../images/card/society_card3.svg);
      }
    }

    &.bg_4 {
      &::before,
      &::after {
        background-image: url(../images/card/society_card4.svg);
      }
    }

    &.bg_5 {
      &::before,
      &::after {
        background-image: url(../images/card/society_card5.svg);
      }
    }
    &.bg_1_lg {
      &::before,
      &::after {
        background-image: url(../images/card/society_card1_lg.svg);
      }
    }

    &.bg_2_lg {
      &::before,
      &::after {
        background-image: url(../images/card/society_card2_lg.svg);
      }
    }

    &.bg_3_lg {
      &::before,
      &::after {
        background-image: url(../images/card/society_card3_lg.svg);
      }
    }

    .col-logo,
    .col-dec {
      position: relative;
      z-index: 1;
    }

    .col-logo {
      margin-right: 15px;

      img {
        width: 64px;
      }
    }

    .col-dec {
      strong {
        font-size: 14px;
        font-weight: 700;
        line-height: 1.2;
        color: rgba(23, 33, 96, 1);
        display: block;
        margin-bottom: 3px;
      }

      .flex-ul {
        dl {
          display: inline-flex;
          color: rgba(117, 117, 117, 1);
          margin-right: 15px;

          &:last-child {
            margin-right: 0;
          }

          dt {
            position: relative;
            padding-left: 10px;
            margin-right: 3px;

            &::before {
              content: "";
              display: block;
              width: 3px;
              height: 3px;
              border-radius: 100%;
              background: rgba(117, 117, 117, 1);
              position: absolute;
              left: 3px;
              top: 50%;
              transform: translate(0, -50%);
            }
          }
        }
      }
    }
    /*&.bg_1 {
      background-image: url('../images/card/society_card1.svg');
    }

    &.bg_2 {
      background-image: url('../images/card/society_card2.svg');
    }

    &.bg_3 {
      background-image: url('../images/card/society_card3.svg');
    }*/
  }

  &.info {
    width: 100%;
    height:auto;
    position: relative;
    background: #fbfbfb;
    border: 1px solid rgba(232, 232, 232, 1);
    border-radius: 12px;
    overflow: hidden;
    padding: 16px 5px 24px;
    justify-content: center;

    &.training {
      width: 50%;
    }

    .info-flex {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 -43px;

      .col-info {
        padding: 0 43px;
        position: relative;

        &:nth-child(2)::before {
          content: "VS";
          width: 20px;
          font-size: 10px;
          color: rgba(51, 51, 51, 1);
          font-weight: 700;
          line-height: 1;
          display: block;
          padding-bottom: 20px;
          background: url("../images/icon/icon_vs.svg") center bottom no-repeat;
          background-size: 18px auto;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(-50%, -50%);
        }

        & > dl {
          min-width: 95px;

          & > dt {
            color: rgba(0, 35, 101, 1);
            font-size: 12px;
            font-weight: 700;
            padding-bottom: 5px;
            text-align: center;
          }
        }

        .team-box {
          background: rgba(255, 255, 255, 1);
          border: 1px solid rgba(233, 233, 233, 1);
          border-radius: 12px;
          padding: 12px 10px;
          text-align: center;
          position: relative;

          &::after {
            content: "";
            display: block;
            width: 20px;
            height: 20px;
            background: url("../images/icon/ico_bedit.svg") center no-repeat;
            background-size: cover;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 50%);
          }

          img {
            height: 30px;
          }

          .team {
            margin-top: 6px;
            white-space: nowrap;
            font-size: 11px;
            color: rgba(51, 51, 51, 1);
          }
        }
      }
    }
  }

  &.live {
    .card_top {
      .live_icon {
        img {
          display: block;
        }
      }
    }

    .game-info {
      display: none;
    }

    .stadium,
    .game_date {
      display: block;
    }

    .game_score {
      font-size: 0.75rem;
      padding: 5px 0;
    }

    .card-team {
      justify-content: center;
      width: 100px;
      flex-direction: column;
      align-items: center;
      grid-gap: 10px;
      gap: 10px;
      font-size: 0.625rem;
      text-align: center;

      dt {
        img {
          margin: 0 auto;
          height: 30px;
        }
      }

      dd {
        color: rgba(0, 35, 101, 1);
        font-weight: 600;
        padding: 0;
      }

      &.underline {
        dd {
          text-decoration: underline;
        }
      }
    }
  }
}

.nodata-box,
.bba-card {
  width: 100%;
  margin: 0 auto;
  border: 1px solid rgba(233, 233, 233, 1);
  border-radius: 16px;
  text-align: center;
  padding: 40px 10px;
  color: rgba(117, 117, 117, 1);
}

.nodata-box {
  max-width: 284px;
  background: #f7f7f7;

  p {
    position: relative;
    padding-top: 57px;
    white-space: pre-wrap;

    &::before {
      content: "";
      width: 47px;
      height: 47px;
      border-radius: 100%;
      background: rgba(221, 243, 240, 1) url("../images/icon/logo symbol.svg")
        center no-repeat;
      background-size: 30px auto;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, 0);
    }
  }
}

.bba-card {
  max-width: 100%;
  text-align: left;
  padding: 18px 22px;
  background: #fff;

  dt {
    border-bottom: 1px solid rgba(233, 233, 233, 1);
    padding-bottom: 8px;
    margin-bottom: 8px;
    color: rgba(51, 51, 51, 1);
    font-weight: 700;
  }

  .dec {
    color: rgba(102, 102, 102, 1);
    max-height: 42px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .time {
    display: inline-block;
    margin-top: 8px;
    color: rgba(137, 137, 137, 1);
  }

  &.active {
    border-color: rgba(233, 243, 250, 1);
    background: rgba(241, 250, 254, 1);

    dt {
      border-bottom-color: rgba(197, 221, 236, 1);
    }

    .dec {
      max-height: 100%;
      display: block;
      -webkit-line-clamp: none;
    }
  }
}

//bbs-list
.bbs-list {
  & > li {
    margin-bottom: 16px;
    width: 100%;

    &:last-child {
      margin-bottom: 0;
    }

    & > * {
      width: 100%;
    }

    .nodata-box {
      max-width: 100%;
    }
  }
}

//schedule-container
.schedule-container {
  & > li {
    margin-bottom: 26px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .schedule-dl {
    & > dt {
      color: rgba(51, 51, 51, 1);
      padding-bottom: 7px;
      border-bottom: 1px solid rgba(229, 229, 229, 1);
      margin-bottom: 14px;
    }
  }
}

// member-card

.member-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  gap: 8px;
  width: 100px;
  height: 173px;
  background: #fff;
  border-radius: 8px;
  position: relative;
  cursor: pointer;

  .member-photo {
    width: 100px;
    height: 110px;
    border-radius: 8px;
    overflow: hidden;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .back-number {
      position: absolute;
      top: 3px;
      right: 3px;
      font-weight: 700;
      font-size: 0.875rem;
      padding: 2px 4px;
      border-radius: 4px;
    }
  }

  .member-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 0 6px;
    gap: 4px;
    width: 100px;
    height: 55px;

    .name-position {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      width: 98px;

      .name {
        font-weight: 700;
        font-size: 0.8125rem;
        line-height: 16px;
        color: #333333;
        margin-right:5px;
      }

      .position {
        font-size: 0.75rem;
        line-height: 16px;
        color: #666666;
      }
    }

    .info {
      display: flex;
      flex-direction: column;
      width: 100%;
      background: #fafafa;
      border-radius: 4px;
      padding: 6px 0;

      .school-info,
      .birth-info {
        display: flex;
        justify-content: space-between;
        padding: 0 8px;

        .label {
          font-size: 0.5rem;
          line-height: 10px;
          color: #757575;
        }

        .value {
          font-size: 0.5rem;
          line-height: 10px;
          color: #757575;
        }
      }
    }

    .parent {
      width: 100%;
      text-align: center;
      font-size: 0.75rem;
      line-height: 16px;
      color: #666666;
    }
  }
}

// memberInfo-card
.mem_infobox {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 20px;
  margin: 14px 0;
  gap: 10px;
  width: 100%;
  height: 80px;
  position: relative;

  background: #f0f5f8;
  border-radius: 8px;

  .edit_btn {
    position: absolute;
    top: 10px;
    right: 10px;

    &.arrow {
      top: 17px;
      left: 150px;
    }
  }

  .line {
    width: 16px;
    height: 1px;
    background-color: #757575;
  }

  .container {
    display: flex;
    align-items: center;
    padding: 0;
    gap: 15px;

    .mam_wrapper {
      display: flex;
      align-items: center;
      padding: 0;
      gap: 10px;

      .logo_ball {
        width: 50px;
        height: 50px;
        position: relative;

        img {
          content: "";
          width: 50px;
          height: 50px;
          border-radius: 100%;
        }
      }

      .mem_info {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
        gap: 5px;

        .name {
          font-weight: 600;
          font-size: 0.875rem;
          color: #333333;
        }

        .position {
          padding: 4px 10px;
          gap: 10px;
          width: 63px;
          height: 20px;
          background: #f7f7f7;
          border: 1px solid #e9e9e9;
          border-radius: 100px;
          font-size: 0.75rem;
          color: #333333;
        }
      }
    }
    .arrow_wrapper {
      margin-left: auto;
    }
  }
}

// 카드 link
.f1 a,
.card-flex li a,
.card_slider a {
  text-decoration: none;
  width:100%;
}

