.termPage {
  width: 100%;

  .join_wrapper {
    .join_container {
      padding-top:35px;

      .term_title {
        font-size: 1.25rem;
        color: #333333;
        font-weight: 700;
        text-align: center;
        padding-bottom: 32px;
      }

      .term_content {
        width:100%;
        max-width:320px;
        margin:0 auto;
        // background-color: #f7f7f7;
        border-radius: 16px;
        padding: 16px;
        font-size: 0.8125rem;
        color: #333333;
        line-height:1.6;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .termPage {
    width: 100%;

    .join_wrapper {
      margin: 0;
      padding:0 15px;
      padding-top:12px;

      .join_container {
        background:rgba(247, 247, 247, 1);
        border-radius: 16px;
        padding:16px;

        .term_content {
          max-width:100%;
          padding:0;
        }
      }
    }
  }
}
