// teamHome
.subHome_section {
  position: relative;

  .team_info_wrapper {
    width: 100%;
    max-width: 1008px;
    height: 242px;
    background-color: #ffffff;
    border-radius: 16px;
    margin-top: -50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 1;

    .team_info {
      padding: 22px;
      gap: 12px;
    }
  }

  .team_info {
    img {
      width: 50px;
      height: 50px;
    }

    .team_name {
      text-align: center;
      font-size: 1.125rem;
      font-weight: 700;
      color: #050065;
      line-height: 20px;

      &.left {
        text-align: left;
      }
    }

    .team_name a {
      font-size: 0.8125rem;
      font-weight: 400;
      text-decoration: none;
      color: #333333;
    }
  }

  .schedule_label,
  .clip_label {
    padding: 10px 0;
  }

  .video_section {
    background-color: #fff;
  }

  .video_section .card_slider,
  .clip_section .card_slider {
    flex-wrap: wrap;

    .card_wrapper {
      margin: 12px 0px;
    }
  }

  // 협회
  &.ass {
    // 협회 홈
    .team_info {
      .team_image_wrapper {
        position: relative;

        .chk {
          width: 20px;
          height: 20px;
          position: absolute;
          bottom: 0px;
          right: 0;
        }
      }
    }
  }

  // 협회 정보 03000000
  .team_info_chk {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 780px;
    font-size: 1rem;
    font-weight: 700;

    .team_info_content {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 5px;
      flex-grow: 1;
    }

    .team_image_wrapper {
      position: relative;

      img {
        width: 33px;
        height: 33px;
      }

      .chk {
        width: 12px;
        height: 12px;
        position: absolute;
        bottom: 0px;
        right: 0;
      }
    }

    Button {
      margin-left: auto;
    }
  }

  // 단체

  &.grp {
    .team_info {
      .team_image_wrapper {
        position: relative;

        .chk {
          width: 20px;
          height: 20px;
          position: absolute;
          bottom: 0px;
          right: 0;
        }
      }
    }
  }

  // 단체 폐쇄 (본점)
  &.gch {
    .team_info_wrapper {
      height: auto;
      .team_info {
        padding-bottom: 0px;
        .team_image_wrapper {
          position: relative;

          .chk {
            width: 20px;
            height: 20px;
            position: absolute;
            bottom: 0px;
            right: 0;
          }
        }
      }
    }
  }

  // 대회 / 리그
  &.eve {
    .team_info_eve {
      width: 100%;
      max-width: 560px;
      display: flex;
      justify-content: space-between;
      padding: 22px 10px 10px 10px;

      .info_btn_wrapper {
        gap: 5px;
        button {
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 4px;
          width: 67px;
          height: 25px;
          padding: 6px 8px;
          font-size: 0.6875rem;
          color: #333333;
          border-radius: 12px;
          background-color: #f2f2f2;
        }
      }

      .info_a_wrapper {
        gap: 5px;
        position: relative;

        a {
          font-size: 0.75rem;
          font-weight: 400;
          text-decoration: none;
          color: #666666;
        }
        .bball_wrap {
          position: inherit;
          top: 0;
          right: 0;
          z-index: 101;
          cursor: pointer;

          img {
            width: 7px;
            height: 4px;
          }
        }
      }
    }
    .info_container {
      width: 100%;
      max-width: 540px;

      .info_item {
        &.match {
          border-right: 0.5px solid #d9d9d9;
          a {
            display: flex;
            flex-direction: column;
            gap: 9px;
            img {
              width: 32px;
              height: 18px;
            }
          }
        }
      }
    }

    .schedule_date_label {
      font-size: 0.75rem;
      font-weight: 700;
      color: #333333;
      padding-bottom: 12px;
    }

    .image_container {
      gap: 16px;
      padding: 20px 0;
      .image_wrapper {
        border-radius: 8px;
        img,
        .m-slide-image {
          // max-width: 305px;
          // max-height: 152px;
          border-radius: 8px;
        }
      }
    }
  }
}

.subHome_m_icon {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 10;
  display: flex;
  gap: 5px;

  img {
    width: 25px;
    height: auto;
  }
}

