.subHome_section.teamInfo {
  flex-direction: column;
  background-color: #f7f7f7;
  padding: 24px 16px;

  .team_title {
    font-size: 1rem;
    font-weight: 700;
  }

  .team_inforamtion {
    width: 100%;
    max-width: 780px;
    margin: 16px auto;
    padding: 20px 16px;
    background-color: #fff;
    border: 1px solid #e9e9e9;
    border-radius: 12px;
  }

  .team_inforamtion.edit {
    background-color: #f7f7f7;
    border: none;
    padding: 0;
    margin: 0;
  }

  .team_inforamtion.edit .teamInfo_edit_wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 14px;
    gap: 14px;
    align-items: flex-start;
    justify-content: space-around;
  }

  .team_inforamtion.edit .form-box {
    padding: 0;
  }

  .team_inforamtion.edit .form-box .col-right {
    display: flex;
    justify-content: flex-end;
    font-size: 0.6875rem;
    color: #666666;
    margin-top: -3px;
  }

  .info-row {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding: 10px;
    border-bottom: 0.5px solid #e0e0e0;
  }

  .info-row:last-child {
    border-bottom: none;
  }

  .info-row a {
    text-decoration: underline;
  }

  .label,
  .label-toggle {
    width: 15%;
    color: #757575;
    font-size: 0.8125rem;
    font-weight: 600;
  }

  .label-toggle {
    display: flex;
    gap: 10px;
    align-items: center;
  }

  .info,
  .info-btn {
    width: 85%;
    font-size: 0.75rem;
    color: #333;

    img {
      width: 18px;
      height: 18px;
      padding: 0 0 3px 5px;
    }
  }

  .info-btn {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
  }
}

.subHome_section.etcInfo {
  flex-direction: column;
  background-color: #fff;
  padding: 24px 16px;

  &.ass {
    // padding-bottom: 50px;
  }

  .etcInfo_wrapper {
    width: 100%;
    max-width: 780px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 24px;

    .etcInfo_container {
      width: 100%;
      display: flex;
      flex-direction: column;

      .ico.logo {
        font-size: 0.8125rem;
        font-weight: 700;
        display: flex;
        justify-content: space-between;

        &::before {
          width: 16px;
          height: 10px;
          background-image: url("../images/icon/ico_hogak.svg");
        }
      }

      .ico {
        display: inline-block;
        position: relative;
        padding-left: 20px;

        &::before {
          content: "";
          display: block;
          width: 17px;
          height: 20px;
          background-size: cover;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(0, -50%);
        }
      }

      .etcInfo_content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 16px 18px;
        margin-top: 8px;
        gap: 10px;
        width: 100%;
        background-color: #f7f7f7;
        border-radius: 12px;

        div {
          display: flex;
          justify-content: space-between;
          align-items: baseline;
          width: 100%;

          .label {
            width: 15%;
            color: #757575;
            font-size: 0.8125rem;
            font-weight: 600;
          }

          .info {
            width: 85%;
            font-weight: 400;
            font-size: 0.75rem;
            line-height: 17px;
            color: #333333;
          }
        }
      }
    }
  }
}

