// 04020201
.subHome_section.address {
  flex-direction: column;
  background-color: #fff;
  padding: 24px;

  .address_wrapper {
    width: 100%;
    max-width: 780px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 24px;

    .addressList {
      .inner {
        display: flex;
        flex-direction: column;
        gap: 16px;
        padding-bottom: 16px;

        thead {
          width: 100%;
          height: 36px;
          border-bottom: 0.5px solid #e9e9e9;
          cursor: pointer;

          .header {
            height: 100%;

            th {
              padding: 2px 8px;
              font-size: 0.8125rem;
              font-weight: 700;
            }
          }
        }

        tbody {
          width: 100%;
          display: flex;
          flex-direction: column;
          gap: 16px;

          .border_bt {
            padding-bottom: 15px;
            border-bottom: 0.5px solid #e9e9e9;
            cursor: pointer;
          }

          tr {
            display: flex;
            justify-content: space-between;
            a {
              width: 100%;
              color: #333333;
              text-decoration: none;
              cursor: pointer;

              td {
                width: 100%;
                padding: 2px 8px;
                font-size: 0.75rem;
                font-weight: 600;
                color: #333333;

                .addText {
                  font-weight: 400;
                  color: #999999;
                  padding-top: 8px;
                }
              }
            }

            .btn_wrapper {
              width: 10%;
              display: flex;
              justify-content: end;
              cursor: pointer;
              padding-top: 5px;
            }
          }
        }
      }
    }
  }
}

