.sitemap-section {
  padding-top:20px;

  header {
    padding: 10px 0;
    display:flex !important;
    justify-content: space-between;

    h1 {
      display: inline-block;
      line-height: 1;
      font-size: 20px;
      font-weight:700;
    }
    .right {
      display:inline-flex;

      & > span {
        margin-left: 10px;
        display: inline-block;

        &::before {
          content: "";
          display: inline-block;
          width: 10px;
          height: 10px;
          margin-right: 5px;
          vertical-align: middle;
        }
        &.pub::before {
          background: #999;
        }
        &.ed::before {
          background: #666;
        }
        &.up::before {
          background: yellow;
        }
        &.ing::before {
          background: #fc971f;
        }
      }

    }
  }

  table {
    border-top: 1px solid #595757;
    border-left: 1px solid #595757;
    font-size: 13px;
    width: 100%;

    th,
    td {
      text-align: center;
      border-bottom: 1px solid #595757;
      border-right: 1px solid #595757;
    }

    th {
      background: #ddd;
    }

    td {
      text-align: center;

      p {
        text-align: left;
        margin:0;
      }
    }

    tr {
      &.header td {
        text-align: left;
        background: #ddd;
        font-weight: 700;
      }

      &.ing td {
        background: #fc971f !important
      }

      &.ed td {
        background:rgba(221, 221, 221, 0.4)
      }

      &.even td {
        background: rgba(0,0,0,0.1)
      }

      &.up td {
        background: yellow
      }
    }
  }

  .inner {
    border-top:0;
    border-left:0;

    tbody {
      tr:last-child {
        td {
          border-bottom:0;
        }
      }
    }

    th,
    td {
      padding: 3px 5px;

      &:last-child {
        border-right:0;
      }
    }
  }

  a {
    text-decoration: underline !important;
  }
}

