// page-search
.page-search {
  .sec-kv {
    .img-box {
      height:100px;
    }
  }
}

.search-container {
  width:100%;
  background:#fff;
  /*position:fixed;
  top:69px;
  left:0;*/

  .form-wrap {
    .btn-flex {
      padding-right:10px;
    }
  }
}

.search-form {
  padding-top:12px;
  margin-bottom:0;
}

.lately-container {
  padding:14px 0;

  & > dt,
  & > dd {
    display:block;
    width:100%;
  }

  & > dt {
    padding-bottom:14px;
    display:flex;
    justify-content:space-between;
  }
}

.lately-tag {
  display:inline-flex;
  width:100%;
  overflow-x:auto;

  & > li {
    margin-right:8px;
    margin-bottom:3px;

    &:last-child {
      margin-right:0;
    }
  }
}

.bubbletab-flex {
  align-items: flex-start;

  .bubble-tab-container {
    margin-left:5px
  }
}

.sec-video {
  background:#F7F7F7;

  .video-wrapper {
    padding:18px 0;

    .list-header {
      justify-content: space-between;
      padding-bottom:6px;
    }


    &.full {
      & > li {
        width: 50%;

        &:last-child {
          margin-bottom:15px;
        }
      }
    }
  }

  .card-flex {
    & > li {
      width:33.33%;
      margin-bottom:0;
      padding:6px;
    }
  }
}

@media screen and (max-width: 880px) {
  .sec-video {
    .card-flex {
      & > li {
        width:50%;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .sec-video {
    .card-flex {
      & > li {
        width:100%;
      }
    }
  }
}

.select-flex {
  display:inline-flex;

  & > li {
    margin-right:4px;

    &:last-child {
      margin-right:0;
    }
  }
}

.top-select {
  padding:8px 0;
  background:#fff;

  .select-flex {
    width:100%;
    overflow: hidden;
    overflow-x:auto;
  }

  .bubble-tab-container {
    padding-bottom:0;
  }
}

.sticky {
  .top-select {
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  }
}