label.btn {
  margin: 0;
}

i.text-primary {
  color: #007bff !important;
}

.checkbox.checkbox-css.disabled {
  opacity: 0.5;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

/* margin */
.mt-2r {
  margin-top: 2rem;
}

/* nowrap */
.nowrap {
  white-space: nowrap;
}

/* label, input */
div.checkbox label,
div.radio label {
  cursor: pointer;
}
.radio.radio-css,
.checkbox.checkbox-css {
  margin-right: 15px;
}

/* check-box */
.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  gap: 8px;
}

.checkbox input {
  appearance: none;
  box-sizing: border-box;
  border: 1px solid #bdbdbd;
  cursor: pointer;
}

.checkbox label {
  color: #000000;
}

.checkbox label a {
  color: #000000;
}

.checkbox.main-checkbox {
  margin-bottom: 24px;
  transform: translateX(-4px);
}

.checkbox.square.small input {
  width: 20px;
  height: 20px;
  border-radius: 2px;
  background-color: white;
}

.checkbox.square.small label,
.checkbox.circle.small label {
  font-size: 0.8125rem;
}

.checkbox.square.large input {
  width: 24px;
  height: 24px;
  border-radius: 2px;
  background-color: white;
}

.checkbox.square.large label,
.checkbox.circle.large label {
  font-size: 1rem;
  font-weight: 700;
}

.checkbox.square.small input:checked,
.checkbox.square.large input:checked {
  border-color: transparent;
  background-color: #304ffe;
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='white' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox.square.small input:not(:checked),
.checkbox.square.large input:not(:checked) {
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='gainsboro' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='gainsboro' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox.circle.small input {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
}

.checkbox.circle.large input {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: white;
}

.checkbox.circle.small input:checked,
.checkbox.circle.large input:checked {
  border-color: transparent;
  background-color: #304ffe;
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='white' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox.circle.small input:not(:checked),
.checkbox.circle.large input:not(:checked) {
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='gainsboro' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5 8l3 3 5-5' stroke='gainsboro' stroke-width='2' fill='none'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.checkbox label {
  margin: 0;
}

// navLink

.mobile_nav .nav_link {
  text-decoration: none;
  color: #333333;
  font-size: 0.8125rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobile_nav p {
  margin: 0;
  padding-top: 5px;
}

.mobile_nav .active p {
  color: #13009f;
}

.mobile_nav .nav_link_img {
  width: 30px;
  height: 30px;
}

.mobile_nav .nav_link_img img {
  width: 20px;
  height: 20px;
}

.mobile_nav .active img {
  width: 30px;
  height: 30px;
}

// badge
.badge {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  &.live,
  &.yet {
    width: 51px;
    height: 19px;
    gap: 4px;
    background-color: #002365;
    border-radius: 22px;
    color: #fff;
    font-size: 0.625rem;
    font-weight: 700;
  }

  &.yet {
    background-color: #35b38b;
  }

  &.clip,
  &.game {
    width: 51px;
    height: 19px;
    border-radius: 500px;
    font-size: 0.625rem;
    font-weight: 700;
    text-align: center;
  }

  &.clip {
    background-color: #fff;
    color: #002365;
  }

  &.game {
    background-color: #5890eb;
    color: #fff;
  }
}

.badge_live {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #ff0000;
}

// toggle
.toggle {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 2px;
  gap: 3px;

  width: 36px;
  height: 20px;

  border-radius: 100px;
  background-color: #d2d5da;

  cursor: pointer;

  &.checked {
    background-color: #2563eb;
  }

  .switch {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #ffffff;
    transition: transform 0.3s;

    &.checked {
      transform: translateX(100%);
    }
  }
}

// bubbleTab
.bubble-tab-container {
  width: 100%;
  overflow: hidden;
  overflow-x: auto;
  display: inline-flex;
  padding: 0 0 12px 0;
  gap: 5px;
  position: relative;
  transition: position 0.3s;

  &.fixed {
    position: fixed;
    top: 36px;
    left: 0;
    width: 100%;
    background-color: white;
    padding: 5px 0;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
}

.bubble-tab,
.bubble-tab .select-box {
  min-width: 70px;
  padding: 0 10px;
  height: 30px;
  border: 0.5px solid #d3d3d3;
  border-radius: 20px;
  background-color: #fff;
  font-size: 0.8125rem;
  color: #333333;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s;
  text-align: center;
}

.bubble-tab {
  position: relative;

  &:active,
  &:hover,
  &:focus {
    color: #fff;
    background-color: #333333;

    & > .btn {
      color: #fff !important;
      background-color: #333333;

      .ico-down::before {
        background-image: url("../images/icon/icon_arrow_down.svg");
      }
    }
  }

  &.active,
  &.active .select-box {
    background-color: #333333;
    border-color: #333333;
    color: #ffffff;

    .btn {
      color: #fff;

      .ico-down::before {
        background-image: url("../images/icon/icon_arrow_down.svg");
      }
    }
  }

  .select-box {
    background-color: #fff;
    color: #333333;

    &:focus {
      background-color: #333333;
      border-color: #333333;
      color: #ffffff;
    }
  }

  .select-box option {
    color: #333333;
    font-size: 0.75rem;
    padding: 5px;
    border: 1px solid #e6e6e6;
    border-radius: 8px;
    background-color: #ffffff;
  }

  .tab-label {
    & > .txt {
      display: inline-block;
      position: relative;
    }

    .cnt {
      color: rgba(255, 2, 2, 1);
      display: inline-block;
      margin-left: 3px;
    }
  }

  .btn {
    font-weight: 400;
    color: #333;
    padding: 0;
    text-align: center;
    white-space: nowrap;
    background-color: transparent;

    .ico-down::before {
      background-image: url("../images/icon/icon_arrow_down_black.svg");
    }
  }

  &._select {
    select {
      padding-right: 15px;
    }

    /*&::before {
      content: "";
      display: block;
      width: 12px;
      height: 7px;
      background: url("../images/icon/icon_arrow_down_black.svg") center
        no-repeat;
      background-size: cover;
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translate(0, -50%);
    }*/

    /*&.active {
      &::before {
        background-image: url("../images/icon/icon_arrow_down.svg");
      }
    }*/
  }

  select {
    height: 100%;
    width: 100%;
    background: transparent;
    border: 0;
    text-align: center;
    font-size: 14px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  &.active {
    select {
      color: #fff;
    }
  }
}

@media screen and (max-width: 480px) {
  .bubble-tab {
    &._select {
      &::before {
        display: none;
      }
    }
  }
}

// card
.slider-container {
  margin: 0 -6px;
}

.card_slider {
  padding: 0 6px;
}

.card_wrapper {
  width: 100%;
  /*width: 328px;
  min-width: 320px;*/
  height: 150px;
  gap: 12px;
  border-radius: 12px;
  /*margin: 0 5px;*/

  &.calc {
    width: calc(33.3333% - 10px);
    margin-bottom: 12px;
  }

  .live_icon {
    img {
      display: block;
      margin-top: 3px;
    }
  }

  .game_score {
    width: 100px;
    line-height: 1;
    padding: 5px 0;
    border-radius: 8px;
    background-color: #f4f4f4;
    color: #474747;
    font-size: 0.75rem;
    font-weight: 700;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
    position: relative;

    .score {
      &._win {
        color: #000;
      }
    }
  }

  // card_live
  &.live {
    border: 0.5px solid #d5d5d5;
    background-color: #fefeff;

    .card_top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 44px;
      border-bottom: 0.5px solid #d5d5d5;
      margin: 0 10px;
      z-index: 20;

      .col-left {
        display: inline-flex;

        .card_title {
          font-size: 0.75rem;
          font-weight: 700;
          color: #333333;
          margin-left: 5px;
        }

        .live_icon img {
          width: 17px;
        }
      }
    }

    .card_bottom {
      display: flex;
      justify-content: space-evenly;
      padding: 22px;

      .card_team {
        width: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        font-size: 0.625rem;
        color: #333333;

        img {
          width: 28px;
          height: 28px;
        }
      }

      .score_wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 3px;

        .stadium {
          font-size: 0.5625rem;
          font-weight: 600;
        }

        .game_date {
          font-size: 0.5625rem;
          font-weight: 400;
          text-align: center;
          color: #757575;
        }
      }
    }
  }

  // card_schedule
  &.schedule {
    .card_top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 38px;
      line-height: 29px;
      border-radius: 12px 12px 0 0;
      padding: 8px 14px;
      color: #fff;
      background-color: #00153b;
      font-size: 0.75rem;
      font-weight: 700;

      &.progress {
        background-color: #00153b;
        color: #fff;
      }

      &.schedule {
        background-color: #84dffe;
        color: #333;
      }

      &.end {
        background-color: #cfd1d6;
        color: #333;
      }
    }

    .schedule_date {
      font-size: 0.625rem;
      font-weight: 400;
    }

    .card_bottom {
      display: flex;
      height: 112px;
      justify-content: center;
      border-radius: 0 0 12px 12px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  // card_clip
  &.clip {
    .card_top.clip {
      display: flex;
      justify-content: space-between;
      height: 60%;
      border-radius: 12px 12px 0 0;
      padding: 10px;
      color: #fff;
      background-color: transparent;
    }

    .card_bottom {
      display: flex;
      flex-direction: column;
      height: 40%;
      justify-content: space-between;
      border-radius: 0 0 12px 12px;
      background-color: rgba(20, 20, 20, 0.85);
      overflow: hidden;
      padding: 3px 14px 9px 14px;
      color: #fff;
      background-size: cover;

      .clip_title {
        font-size: 0.6875rem;
        font-weight: 700;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid rgba(83, 97, 202, 0.4);
        line-height: 1;
        padding-bottom: 3px;
      }

      .team_info {
        display: inline-block;
        padding: 4px;
        text-align: center;
        font-size: 0.5rem;
        font-weight: 400;
        background-color: rgba(35, 49, 130, 0.9);
        border-radius: 4px;
      }

      .clip_content {
        padding-top: 3px;
        font-size: 0.625rem;
        font-weight: 400;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .btt {
          display: inline-flex;
          align-items: center;

          .live_icon {
            margin-left: 5px;
          }
        }
      }

      .clip_stadium {
        display: inline-block;
        padding: 4px;
        text-align: center;
        font-size: 0.5rem;
        font-weight: 400;
        background-color: rgba(5, 8, 23, 0.9);
        border-radius: 4px;
      }

      & > div {
        display: flex;
        justify-content: space-between;
        width: 100%;
      }
    }
  }

  // card_video
  &.video {
    border: 1px solid #d5d5d5;

    .card_top {
      &.video {
        display: flex;
        justify-content: space-between;
        height: 50%;
        border-radius: 12px 12px 0 0;
        padding: 10px 15px;
        color: #fff;
        background-color: transparent;

        .title {
          height: 19px;
          border-radius: 44px;
          padding: 2px 10px;
          background-color: rgba(23, 24, 49, 0.6);
          color: #ffffff;
          font-size: 0.6875rem;
          font-weight: 400;
        }
      }

      .header {
        justify-content: space-between;
        white-space: nowrap;
        align-items: flex-start;
        width: 100%;

        & > li {
          width: 20px;
          display: inline-flex;

          &:first-child {
            padding-right: 5px;
            width: calc(100% - 20px);
          }

          .badge {
            margin-right: 2px;
          }
        }
      }
    }

    .card_bottom {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      height: 50%;
      padding: 5px 10px;
      background-color: #ffffff;
      border-radius: 0 0 12px 12px;

      .card_team {
        width: 100px;
        height: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 0.625rem;
        color: #333333;

        img {
          width: 28px;
          height: 28px;
        }
      }

      .score_wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 3px;
      }
    }

    .score_wrapper {
      .stadium {
        font-size: 0.5625rem;
        font-weight: 600;
      }

      .game_date {
        font-size: 0.5625rem;
        font-weight: 400;
        text-align: center;
        color: #757575;
      }
    }
  }

  // card_gameSchedule
  &.gameSchedule {
    border: 0.5px solid #d5d5d5;
    background-color: #fefeff;
    // width: 590px;
    // height: 125px;
    padding: 0 10px;

    .card_top {
      &.gameSchedule {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 44px;
        border-bottom: 0.5px solid #d5d5d5;
        margin: 0 10px;
        z-index: 20;

        .col-left {
          display: inline-flex;

          .card_title {
            font-size: 0.75rem;
            font-weight: 700;
            color: #333333;
            margin-left: 5px;
          }
        }
        .game-info {
          gap: 5px;
          .stadium {
            width: 75px;
            height: 24px;
            padding: 3px 5px;
            border: 0.5px solid #cedeee;
            background-color: #cedeee;
            border-radius: 24px;
            font-size: 0.75rem;
            font-weight: 600;
            white-space: nowrap;
          }
        }
      }
    }

    .card_bottom {
      padding: 20px 10px;
      &.gameSchedule {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .card-team {
          width: 30%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          gap: 10px;
          font-size: 0.8125rem;
          font-weight: 600;
          color: #002365;

          img {
            width: 30px;
            height: 30px;
          }
        }

        .score_wrapper {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          gap: 3px;

          .game_score {
            width: 150px;
            height: 43px;
            border-radius: 6px;
            &.cancel {
              font-size: 0.6875rem;
              font-weight: 700;
              color: #ff0000;
            }
            .score {
              font-size: 1.25rem;
              font-weight: 700;
              color: #b6b6b6;
              &._win {
                color: #333333;
              }
            }
          }

          .game_date {
            font-size: 0.5625rem;
            font-weight: 400;
            text-align: center;
            color: #757575;
          }
        }
      }
    }
  }
}

.card_slider {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &.flexStart {
    justify-content: flex-start;
  }
}

@media screen and (max-width: 480px) {
  .card_slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px;
    &.flexStart {
      justify-content: flex-start;
    }
  }
  .card_wrapper {
    height: 125px;

    &.live {
      .card_bottom {
        padding: 14px;
      }
    }

    &.schedule {
      .card_top {
        height: 29px;
      }
      .card_bottom {
        height: 96px;
      }
    }

    &.clip {
      .card_top.clip,
      .card_bottom.clip {
        height: 50%;
      }
    }

    &.video {
      .card_bottom {
        padding: 5px 10px;
      }
    }

    &.gameSchedule {
      width: 328px;
      height: 60px;
      padding: 0 5px;

      .card_top {
        &.gameSchedule {
          height: auto;
          border: none;
          margin: 0;
          padding: 3px 0;

          .col-left {
            .card_title {
              font-size: 0.5625rem;
              font-weight: 600;
              color: #002365;
              margin-left: 5px;
            }
          }
          .game-info {
            gap: 5px;

            .stadium {
              width: auto;
              height: auto;
              padding: 0px 5px;
              border: none;
              color: #5a4f4f;
              background-color: #fff;
              font-size: 0.5625rem;
              font-weight: 400;
              white-space: nowrap;
            }
          }
        }
      }

      .card_bottom {
        padding: 0;
        &.gameSchedule {
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;
          .card-team {
            width: 30%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 0px;
            font-size: 0.5625rem;
            font-weight: 400;
            color: #757575;

            img {
              width: 22px;
              height: 22px;
            }
          }

          .score_wrapper {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 3px;

            .game_score {
              width: 102px;
              height: 22px;
              border-radius: 8px;
              &.cancel {
                font-size: 0.6875rem;
                font-weight: 700;
                color: #ff0000;
              }
              .score {
                font-size: 0.875rem;
                font-weight: 700;
                color: #b6b6b6;
                &._win {
                  color: #333333;
                }
              }
              .schedule_date {
                font-size: 0.5625rem;
                font-weight: 700;
                color: #333333;
              }
            }

            .game_date {
              font-size: 0.5625rem;
              font-weight: 400;
              text-align: center;
              color: #757575;
            }
          }
          .event {
            position: absolute;
            bottom: 2px;
            right: 2px;
          }
        }
      }
    }
  }

  .card_slider {
    display: flex !important;
    justify-content: center;
    &.flexStart {
      justify-content: center;
    }
  }
}

// noti_slider
.noti_wrapper {
  width: 520px;
  padding: 20px 0;

  .slick-track {
    display: flex;
    gap: 10px;
  }

  .noti_slider {
    width: 100%;
    height: 34px;
    border: 0.5px solid #d5d5d5;
    border-radius: 26px;
    background-color: #edf8ff;
    display: flex !important;
    align-items: center;
    gap: 5px;
    padding: 3px 8px;

    .notiIcon {
      width: 10.5px;
      height: 10px;
    }

    .date {
