// image_slider
.image_slider {
  position: relative;
  width: 100%;
  height: 226px;
  overflow: hidden;

  .slide {
    position: relative;
    height: 226px;

    .slide-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      background-color: rgba(0, 0, 0, 0.6);
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.6);
      z-index: 1;

      &.status {
        background-color: rgba(78, 78, 78, 0.6);
      }
    }

    .overlay_txt {
      color: #fff;
      font-size: 0.875rem;
      font-weight: 600;
    }
  }

  .slide-counter {
    width: 46px;
    height: 18px;
    position: absolute;
    bottom: 20px;
    right: 20px;
    background-color: rgba(7, 7, 7, 0.6);
    color: white;
    padding: 4px 16px;
    border-radius: 20px;
    font-size: 0.625rem;
    line-height: 100%;
  }
}

