.slick-slider {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.carousel_wrapper .slick-track {
  display: flex;
  justify-content: center;
}

.carousel_wrapper .slick-slide {
  display: flex;
  justify-content: center;
}

.carousel_wrapper .slick-slider img {
  /*width: 100%;
  height: 176px;*/
  object-fit: cover;
}

@media (max-width: 480px) {
  .carousel_wrapper .slick-slider {
    max-width: 480px;
    margin: 0 auto;
  }
}

.carousel_wrapper .slick-slider .slick-dots {
  bottom: 10px;
}

.carousel_wrapper .slick-slider .slick-dots li button:before {
  font-size: 8px;
  opacity: 100;
  color: #d9d9d9;
}

.carousel_wrapper .slick-slider .slick-dots li.slick-active button:before {
  color: #ffffff;
  opacity: 100;
  z-index: 10;
}

.carousel_wrapper .slick-slider .slick-dots li {
  margin: 0;
}
