/* Zeroize */
body,
div,
dl,
dt,
dd,
li,
form,
input,
p,
blockquote,
th,
td,
figure,
figcaption {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address,
caption,
cite,
code,
dfn,
em,
th,
var {
  font-style: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
  width: 100%;
}

fieldset,
img,
iframe {
  margin: 0;
  padding: 0;
  border: 0;
}

q:before,
q:after {
  content: "";
}

img {
  display: inline-block;
  border: 0 none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: 1em;
}

address,
cite,
em,
dfn {
  font-style: normal;
}

blockquote,
p {
  margin: 0;
}

pre {
  margin: 0;
  font-size: 1em;
}

area:focus {
  border: none;
  outline: none;
  outline-style: none;
  -moz-outline-style: none;
}

button {
  border: 0;
  padding: 0;
  background: transparent;
}

/*a:focus, a:hover,
button:focus, button:hover,
area:focus, area:hover {outline: #008aa2 solid 1px;outline-offset: -1px;}*/
input,
select,
textarea {
  font-family: "NotoSans";
  font-size:13px;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  font-family: inherit;
  font-size: inherit;
  color:#8c939a;
}

input[type="text"]:focus,
input[type="text"]:hover,
input[type="password"]:focus,
input[type="password"]:hover,
select:focus,
select:hover,
textarea:focus,
textarea:hover {
  border-color: #0076cb;
  background: #f3f8fd;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.a11y {
  position: absolute !important;
  left: -10000px !important;
  top: -10000px !important;
  width: 0;
  height: 0;
  font-size: 0;
  display: block;
  visibility: hidden;
}

.ellip {
  display: inline-block;
  width: 95%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: left;
  word-wrap: normal;
}

small {
  font-size: 11px;
}

.tip {
  &.t1 {
    color: rgba(255, 59, 48, 1);
  }
}
.img-box {
  width: 100%;
  background-size: cover;
  background-position: center;
}

.flex {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  &.f2,
  &.f3,
  &.f4,
  &.f5 {
    margin: 0 -5px;

    & > * {
      padding: 0 5px;
    }
  }

  &.f2 {
    & > * {
      width: 50%;
    }
  }

  &.f3 {
    & > * {
      width: 33.33%;
    }
  }

  &.f4 {
    & > * {
      width: 25%;
    }
  }

  &.f5 {
    & > * {
      width: 50%;
    }
  }
}

@media screen and (max-width: 480px) {
  .flex {
    &.f2,
    &.f3,
    &.f4,
    &.f5 {
      margin: 0 -3px;

      & > * {
        padding: 0 3px;
      }
    }
  }
}

