.nodata {
  width: 100%;
  padding-top: 70px;
  padding-bottom: 20px;
  text-align: center;
  font-size: 11px;
  color: #999;
  line-height: 1.2;

  dt {
    font-size: 14px;
    font-weight: 700;
    color: #333;
    margin-bottom: 10px;
    position: relative;
    padding-top: 87px;

    &::before {
      content: "";
      display: block;
      width: 75px;
      height: 75px;
      border-radius: 100%;
      background: #f2f2f2;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
