// tab
.sticky {
  z-index: 100;
}

.sticky-line {
  display: block;
  width: 100%;
  height: 1px;
  background: blue;
  position: fixed;
  z-index: 9999;
}

.tab-container {
  .tabs {
    & > * {
      min-height: 45px !important;
    }
  }

  .tab-nav {
    display: flex;
    padding: 0 15px;
    border-bottom: 1px solid #e5e5e5;
    background: #fff;

    .tab {
      width: 100%;
    }
  }

  &.t4 {
    .tab-nav {
      & > li {
        width: 25%;
      }
    }
  }

  &.t3 {
    .tab-nav {
      & > li {
        width: 33.33%;
      }
    }
  }

  .tabs {
    .tab {
      text-align: center;
      padding: 15px 0;
      font-size: 14px;
      font-weight: 600;
      color: #999;
      line-height: 1;
      position: relative;

      &::before {
        content: "";
        display: block;
        width: 0;
        height: 2px;
        background: linear-gradient(
          90deg,
          rgba(115, 46, 255, 0.8),
          rgba(115, 46, 255, 1)
        );
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        transition-duration: 0.3s;
      }

      &.active {
        &::before {
          width: 100%;
        }
      }
    }

    .tab-content {
      .tab-item {
        padding: 10px 15px;
        background: #fff !important;
      }

      .btt-btn {
        margin: 30px -15px;
        padding: 0 15px;
        background: #fff;
        z-index: 10;
      }
    }
  }

  .tab-item {
    padding: 0;
  }
}

// blit list
.blit-list {
  & > li {
    position: relative;
    padding-left: 10px;

    &::before {
      content: "";
      display: block;
      width: 3px;
      height: 3px;
      border-radius: 100%;
      background: #757575;
      position: absolute;
      left: 0;
      top: 10px;
    }
  }

  &.star {
    & > li {
      &::before {
        content: "*";
        display: inline-block;
        width: auto;
        height: auto;
        border-radius: 0;
        background: transparent;
        color: rgba(117, 117, 117, 1);
        top: 2px;
      }
    }
  }
}

.chk-list {
  & > li {
    text-align: left;
    width: 100%;
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

