// imageUploader
.imageUploader {
  .image_wrapper {
    margin: 24px 0;
    padding: 24px;
    background-color: #f7f7f7;
    border: 1px solid #e9e9e9;
    border-radius: 8px;

    .title {
      font-size: 0.8125rem;
      font-weight: 600;
      color: #666666;
      margin-bottom: 16px;
    }
    .image_container {
      position: relative;
      margin-bottom: 16px;

      .ico-blackdel {
        position: absolute;
        top: -8px;
        right: -8px;
        cursor: pointer;
        z-index: 100;
      }
    }
  }
}

.custom-list {
  .inner {
    padding: 0;

    & > dt {
      background: rgba(247, 247, 247, 1);
      line-height: 26px;
      padding: 0 8px;
      font-weight: 700;
      color: rgba(117, 117, 117, 1);
    }
  }

  .list-ul {
    & > li {
      width: 100%;
      border-bottom: 1px solid rgba(233, 233, 233, 1);
      margin-bottom: 6px;

      &:last-child {
        margin-bottom: 0;
        border-bottom: 0;
      }

      .item-flex {
        width: 100%;

        .item {
          width: 100%;
          padding: 10px 0;
          position: relative;
          color: rgba(51, 51, 51, 1);
          font-weight: 600;
          font-size: 13px;

          & > * {
            width: 100%;
            line-height: 1;
            overflow: hidden;
          }

          .addText {
            font-size: 11px;
            color: rgba(153, 153, 153, 1);
          }
        }
      }

      .item {
        text-decoration: none;
      }

      .arrow,
      .selectLayer {
        padding-right: 20px;
        position: relative;

        &::after {
          content: "";
          display: block;
          width: 5px;
          height: 12px;
          background: url("../images/icon/icon_arrow_right_gray.svg");
          position: absolute;
          right: 0;
          top: 50%;
          transform: translate(0, -50%);
        }
      }
    }

    .rows {
      padding: 6px 0 14px;

      & > li {
        border: 0;
      }

      .item,
      .selectLayer {
        padding: 0;
        padding-left: 10px;
        color: rgba(102, 102, 102, 1);
      }
    }

    .item-flex {
      justify-content: space-between;
    }
  }

  &.setting {
    .inner {
      & > dd {
        padding: 12px 0 20px;
      }
    }

    .list-ul {
      & > li {
        margin-bottom: 0;
        border-bottom: 0;

        .item-flex {
          height: 32px;

          &._toggle {
            .item {
              width: calc(100% - 36px);
            }

            .toggle {
              width: 36px;
            }
          }
        }

        .item {
          height: 100%;
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          padding: 0 8px;
          position: relative;
          color: rgba(51, 51, 51, 1);
          font-weight: 600;
          font-size: 13px;

          & > * {
            width: 100%;
            line-height: 1;
            overflow: hidden;
          }

          &.arrow {
            padding-right: 20px;

            &::after {
              content: "";
              display: block;
              width: 5px;
              height: 12px;
              background: url("../images/icon/icon_arrow_right_gray.svg");
              position: absolute;
              right: 0;
              top: 50%;
              transform: translate(0, -50%);
            }
          }

          .addText {
            font-size: 11px;
            color: rgba(153, 153, 153, 1);
          }
        }
      }

      .item-flex {
        justify-content: space-between;
      }
    }
  }
}

