//촬영
.sec-camera {
  width: 100%;
  height: 100%;
  background: #000;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;

  .camera-container {
    width: 100%;
    max-width: 211px;
    padding-top: 107px;
    margin: 0 auto;
    text-align: center;

    .dec {
      font-size: 15px;
      color: rgba(255, 255, 255, 1);
    }

    .camera-wrapper {
      margin-top: 24px;
      position: relative;
      width: 211px;
      height: 211px;

      .guide {
        display: block;
        width: 18px;
        height: 18px;
        position: absolute;
        z-index: 10;

        &.left-top,
        &.right-btt {
          border-top: 2px solid rgba(18, 255, 97, 1);
          border-left: 2px solid rgba(18, 255, 97, 1);
        }

        &.right-top,
        &.left-btt {
          border-top: 2px solid rgba(18, 255, 97, 1);
          border-right: 2px solid rgba(18, 255, 97, 1);
        }

        &.right-btt,
        &.left-btt {
          transform: rotate(180deg);
        }

        &.left-top {
          top: 0;
          left: 0;
        }

        &.right-top {
          top: 0;
          right: 0;
        }

        &.left-btt {
          bottom: 0;
          left: 0;
        }

        &.right-btt {
          bottom: 0;
          right: 0;
        }
      }

      &::after,
      &::before {
        content: "";
        display: block;
        width: 4px;
        height: 34px;
        background: rgba(18, 255, 97, 1);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
      }

      &::after {
        transform: translate(-50%, -50%) rotate(90deg);
      }

      .camera-wrap {
        display: block;
        width: 100%;
        height: 100%;
        position: relative;
        background: rgba(217, 217, 217, 1);
      }
    }
  }
}

