.qr-form {
  background: rgba(245, 245, 245, 1);
  display: flex;
  justify-content: center;
  padding: 13px 0;
  border-radius: 8px;
  margin-bottom: 20px;

  .form-wrap {
    position: relative;
    width: 22px;

    .bar {
      width: 10px;
      height: 2px;
      background: rgba(51, 51, 51, 1);
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  input {
    height: 18px;
    width: 100%;
    border: 0;
    background: transparent;
    text-align: center;
  }

  input:placeholder-shown + .bar {
    background: rgba(51, 51, 51, 1);
  }

  input:focus + .bar {
    background: transparent;
  }
}

.sec-reserve {
  background: rgba(247, 247, 247, 1);

  .reserve-container {
    & > dt {
      padding-bottom: 10px;
      margin-bottom: 10px;
      border-bottom: 1px solid rgba(233, 233, 233, 1);
    }

    .list-content {
      & > li {
        padding-bottom: 20px;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid rgba(233, 233, 233, 1);

        &:last-child {
          margin-bottom: 0;
          border-bottom: 0;
        }
      }

      .col-right {
        width: 154px;

        .btn-ul {
          margin: 0 -2px;

          & > li {
            padding: 0 2px;
            width: 100%;
          }

          &.f2 {
            & > li {
              width: 50%;
            }
          }
        }

        .btn {
          padding: 8px 0;
        }
      }

      .btn:disabled {
        background: #fff;
        opacity: 0.5;
        color: #ddd;
      }
    }
  }
}

.time-container {
  border: 1px solid rgba(240, 240, 240, 1);
  border-radius: 8px;
  padding: 21px 19px;
  overflow-x: auto;

  .select-list {
    display: inline-flex;

    & > li {
      width: 104px;
      margin-right: 3px;

      &:last-child {
        margin-right: 0;
      }
    }

    .chk-label {
      width: 100%;
      color: rgba(102, 102, 102, 1);

      .txt {
        display: block;
        width: 100%;
        text-align: left;
        padding-bottom: 40px;
        padding-left: 0;
        font-weight: 600;
        font-size: 13px;
        color: rgba(102, 102, 102, 1);
        line-height: 1;

        &::before {
          width: 100%;
          border: 0;
          height: 34px;
          border-radius: 6px;
          background: rgba(134, 177, 230, 1);
          top: auto;
          bottom: 0;
          transform: translate(0, 0);
        }
      }

      :disabled + .txt {
        &::before {
          background: rgba(235, 235, 235, 1);
        }
      }

      :checked + .txt {
        &::before {
          background: rgba(0, 97, 215, 1)
            url("../images/icon/icon_chkecked_big.svg") center no-repeat;
          background-size: 13px auto;
        }
      }
    }
  }
}

.repeat-list {
  margin-left: -4px;
  margin-right: -4px;

  & > li {
    width: 14.28%;
    padding: 0 4px;

    .txt {
      width: 100%;
    }

