  font-size: 12px;
  border: 1px solid rgba(233, 233, 233, 1);
  border-radius: 12px;
  background: rgba(247, 247, 247, 1);
  margin-bottom: 24px;

  .game {
    display: inline-block;
    padding: 3px 0;
    padding-left: 26px;
    position: relative;

    &::before {
      content: "";
      display: block;
      width: 20px;
      height: 20px;
      background: url("../images/card/icon_ball.svg") center no-repeat;
      background-size: cover;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
    }
  }
}

