// loginPage

.loginPage {
  .login_wrapper {
    min-height:500px;

    .m-main {
      max-width:350px;
      height:100%;
      display:flex;
      align-items: center;

      .col-wrap {
        width:100%;
      }
    }

    .login_logo {
      display: flex;
      flex-direction: column;
      align-items: center;

      div {
        font-size: 1.125rem;
        font-weight: 700;
        color:rgba(102, 102, 102, 1);
      }

      .logo_text {
        font-size: 2.8125rem;
        font-weight: 900;
        position:relative;
        padding-top:69px;
        background:url('../images/icon/logo_symbol.svg') center top no-repeat;
        background-size: auto 58px;
        color:#000;
      }
    }
  }

  .login_btn_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 11px;
    padding:50px 0 20px;
  }
}

.login_btn_wrapper {
  max-width:320px;
  width:100%;
  margin:0 auto;
}

.login_btn {
  width:100%;
  font-size: 1.125rem;
  border: none;
  transition: none;
  color: #fff;
  display: flex;
  align-items: center;

  img {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
}

.login_btn.mobile {
  width: 244.5px;
  height: 49.5px;
  font-size: 0.8125rem;
  border-radius: 12px;
}

/* 텍스트 스타일 */
.login_btn {
  span {
    flex: 1;
    text-align: center;
  }

  &:hover {
    color: #fff;
  }

  &.naver {
    background-color: #03c75a;
  }

  &.kakao {
    background-color: #fee500;
    color: #333333;
  }

  &.google {
    background-color:rgba(47, 82, 255, 1);
    color: #fff;
    border: 1px solid rgba(47, 82, 255, 1);
  }

  &.apple {
    background-color: #000000;
  }
}

@media screen and (max-width: 480px) {
  .loginPage {
    background-color: #fff;

    .login_wrapper {
      &._fix {
        height:100vh;
        padding-bottom:0 !important;
        min-height:560px;
      }

      .login_logo {
        div {
          color:#fff;
          font-size:4.17vw;
        }

        .logo_text {
          font-size:11.67vw;
          padding-top:18.54vw;
          background-size: auto 17.7vw;
          color:#fff;
          line-height:1;
          margin-bottom:4.17vw;
        }
      }
    }

    &.splash {
      background:#000 url("../images/login_splash.svg") center top no-repeat;
      min-height: 100vh;
      background-size: cover;
    }

    .login_btn_wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 11px;
      padding:13.75vw 0 20px;

      .login_btn {
        border-radius: 4px;
      }
    }
  }
}

// join
.joinPage {
  width: 100%;

  .join_wrapper {
    position:relative;
    width:100%;
    min-height:680px;
    padding-top:24px;

    .join_container {
      padding-top:70px;
      padding-bottom:37px;
      max-width:540px;
      margin:0 auto;
      background:#fff;
      border-radius: 16px;

      & > .m-main {
        max-width:310px;
      }

      .col-top,
      .join_term_wrapper {
        //padding-left:30px;

        .m-main {
          max-width:334px;
        }
      }

      .btt-btn {
        .m-main {
          max-width:390px;
        }
      }

      .join_info {
        font-size: 1.25rem;
        color: #333333;
        font-weight: 700;
        margin-bottom:0;
      }

      .join_term_wrapper {
        display: flex;
        flex-direction: column;
        gap: 6px;
        align-items: flex-start;
        justify-content: space-around;

        .chk-label {
          margin-right:0;
          width:100%;

          &.main-checkbox {
            margin-bottom:10px;

            & > .txt {
              font-size: 1rem;
              font-weight: 700;
            }
          }

          & > .txt {
            font-size: 13px;
          }

          a {
            text-decoration: underline;
            color: #000;
          }
        }
      }

      .guardian_noti {
        width: 100%;
        padding:10px 18px;
        background-color: #f6f6f6;
        border-radius: 16px;
        font-size: 0.75rem;
        margin-top:36px;
        margin-bottom:27px;

        .dec {
          padding-left:54px;
          position:relative;

          &::before {
            content:"";
            display:block;
            width:37px;
            height:25px;
            background:url(../images/icon/logo_symbol.svg) center no-repeat;
            background-size:cover;
            position:absolute;
            left:0;
            top:0;
          }
        }
      }

      .join_btn_wrapper {
        flex-direction: column;
        gap: 12px;
        margin-top:64px;

        .join_btn {
          width: 100%;
          height: 90px;
          border: 1px solid #e9e9e9;
          border-radius: 12px;
          display: flex;
          align-items: center;
          padding: 0 32px;
          gap: 22px;

          .btn_text-container {
            display: flex;
            flex-direction: column;
            align-items: baseline;
            font-size: 1rem;
            font-weight: 700;
            color: #333333;

            p {
              margin: 0;
              font-size: 0.8125rem;
              font-weight: 400;
              color: #999999;
            }
          }
        }
      }

      .form-box {
        padding:24px 16px;
        border-radius: 16px;

         margin-top:40px;

        .form-group {
          margin-bottom:24px;

          &:last-child{
            margin-bottom:0;
          }
        }
      }


      .checkbox-container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        label {
          line-height: 25px;
          white-space: nowrap;
        }
      }

      .checkbox-description {
        font-size: 0.6875rem;
        color: #333333;
        margin-left:28px;
      }

      &.t2 {
        display:flex;
        flex-wrap:wrap;
        align-content: space-between;

        & > * {
          width:100%;
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .page-wrapper.loginPage,
  .page-wrapper.joinPage {
    background-color: #fff;
  }

  .joinPage {
    min-height:520px;

    .join_wrapper {
      padding:0;

      .join_container {
        padding:0;

        .join_info {
          padding-top:6.84vw;
          margin-bottom:6.25vw;
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .joinPage {
    min-height:0 !important;
    height:auto !important;

    .join_wrapper {
      min-height:560px;

      .join_container {
        .join_info {
          padding-top:36px;
        }
      }
    }
  }
}






