.sec-header {
  h2 {
    .ico {
      &.calendar {
        &::before {
          width: 18px;
          height: 20px;
          background-image: url("../images/icon/icon_calender.svg");
        }
      }
    }
  }
}

.sec {
  &.carousel_section {
    padding: 0;

    .slideer-wrapper {
      width:100%;
    }

    .kv-slider {
      display: block !important;
    }
    .slide-item {
      width: 100%;
      height: 176px;

      .m-main {
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      .img-box {
        height: 100%;
      }

      .col-dec {
        color: #fff;
        white-space: pre-wrap;
        line-height: 1.2;

        dt {
          font-size: 20px;
        }

        dd {
          margin-top: 3px;
          font-size: 12px;
        }
      }
    }
  }

  &.schedule_section {
  }
}

@media screen and (max-width: 480px) {
  .top-fix {
    padding-top:46px !important;

    .top-select {
      width:100%;
      background:#fff;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
      position:fixed;
      left:0;
      right:0;
      top:36px;
      z-index:100;
    }
  }
}