.alert-popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(45, 45, 45, 0.9);
  z-index: 1000;
}

.alert-content {
  background: white;
  border-radius: 8px;
  padding: 20px;
  max-width: 360px;
  width: 100%;
  text-align: center;

  &.p-0 {
    padding: 0;
    max-width: 300px;
  }
}

.alert-logo {
  width: 47px;
  height: 47px;
  margin-bottom: 25px;
}

.alert-title {
  font-size: 1rem;
  font-weight: 700;
  margin: 30px 0 10px 0;
}

.alert-message {
  font-size: 0.8125rem;
  margin: 10px 0 20px 0;
}

.alert-message P {
  padding: 24px 0;
}

.alert-message.nowrap {
  white-space: nowrap;
}

.additional-text-container {
  background-color: #f0f0f0;
  padding: 10px;
  margin: 10px 0;
  border-radius: 4px;
  text-align: left;
}

.button-container {
  display: flex;
  justify-content: space-between;
  gap: 8px;
}

.alert-button {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border: none;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

// guide_alert
.bubble-notice {
  position: absolute;
  width: auto;
  min-width: 280px;
  height: 47px;
  left: 60px;
  top: 93px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  z-index: 5;

  .rectangle {
    position: absolute;
    width: 100%;
    height: 38px;
    left: 0;
    top: 10px;
    background: #e6f7f0;
    border-radius: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px 12px;

    .polygon {
      position: absolute;
      width: 0;
      height: 0;
      left: 50%;
      top: -10px;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 14px solid #e5f7f0;
    }

    .content {
      width: 100%;
      height: 14px;
      font-weight: 400;
      font-size: 0.75rem;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      color: #333333;
      white-space: nowrap;
      overflow: hidden;

      a {
        font-weight: 600;
        text-decoration: none;
        color: #00ad9c;
      }
    }
  }
}
